import React from 'react'
import { Link } from 'react-router-dom'

/**
*
* @mwangihub
* @summary props object should have: 
* - links(Must) = to loop through them: const links = [{name: 'Home', to: '/'},], 
* - object = data to use like title and other information {id: 1, name: 'Alice', country: 'Austria'},, 
* - title = title to display if not in object String, 
* - current(Must) = the current page, String
* - styles = object with styles
* @Function BreadCrumb
* @param {object} props
* @return {JSX} return JSX 
*
*/

const BreadCrumb = props => {
    const { links, object, title, current, styles } = props;
    return (
        <div className="breadcrumbs d-flex align-items-center" style={{}}>
            <div className="container position-relative d-flex flex-column align-items-center">
                {object && <h3>{object.title}</h3>}
                {title && <h3>{title}</h3>}
                <ol>
                    {
                        links && links.map((link, index) => {
                            return <li key={index}><Link to={link.to}>{link.name}</Link></li>
                        })
                    }
                    <li>{current}</li>
                </ol>
            </div>
        </div>
    )
}

export default BreadCrumb