import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Outlet } from 'react-router-dom';
import DevInfo from '../DevInfo';
import redux from '../ReduxMapper';
import Navbar from './container/Navbar';
import { useNavigate } from 'react-router-dom';

class ProfileComponent extends Component {
    componentDidMount() {
        document.querySelector("body").style.background = "#f6f9ff"
    }
    render() {
        const { authLogout, navigate } = this.props;
        const handleLogout = () => {
            navigate("/");
            document.querySelectorAll(".modal-backdrop").forEach(element => {
                element.remove();
            });
            document.querySelector("body").removeAttribute('style')
        }
        return (
            <React.Fragment>
                <DevInfo {...doc} />
                <Navbar />
                <main id="main" className="main">
                    <div className="modal fade" id="logOutModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content">
                                <div className="modal-header border-0">
                                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                </div>
                                <div className="modal-body">
                                    Are you sure you want to Sign out?
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary btn-sm" data-bs-dismiss="modal">Cancel</button>
                                    <button type="button" className="btn btn-primary btn-sm" onClick={e => { authLogout(handleLogout) }}>Sign out</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Outlet />
                </main>
            </React.Fragment>
        )
    }
}
const Profile = props => {
    return (<ProfileComponent {...props} navigate={useNavigate()} />);
};

export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Profile);

const doc = {
    title: "Customer Profile page",
    description: [
        ["1. USER DASHBOARD"],
        ["This is the customers dashboard"],
        ["Less is done here"],
        ["TODO:"],
        ["          - Viewing uncompleted order"],
        ["          - Tracking placed order"],
        ["          - Request refund"],
        ["          - Shipping/Billing Address update"],
        ["          - User Details update"],
        ["          - Contact for help"],
        ["--------------------------------------------------------------------------------------"],
        ["2. ADMIN DASHBOARD"],
        ["  This is the ADMIN dashboard"],
        ["  NOT YET IMPLEMENTED"],
        ["  TODO:"],
        ["          - Do ALL customer can do."],
        ["          - CRUD for; items, orders, cartItems, users, e.t.c"],
        ["--------------------------------------------------------------------------------------"],

    ]
}