import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../Api';

class HomeProducts extends Component {
    componentDidMount() {
        this.props.getHomeProducts()
    }
    render() {
        const { authentication: { isAuthenticated }, products: { homeProductsError, homeProductsLoader, homeProducts }, localCartUpdated, localHomeProductsUpdate } = this.props;
        return (
            <section id="home_products" className="home_products">
                <div className="container">
                    {
                        homeProductsError &&
                        <div className="d-flex justify-content-center">
                            <div className="col-lg-6 col-md-8 col-sm-12">
                                <div className="alert alert-danger">{homeProductsError}</div>
                            </div>
                        </div>
                    }
                    {homeProductsLoader && <div id="preloader" />}
                    {homeProducts && homeProducts.length > 0 &&
                        <React.Fragment>
                            <div className="section-header">
                                <h2>Trending products</h2>
                            </div>
                            <div className="row gy-4">
                                {
                                    homeProducts.map(product => {
                                        return (
                                            <div className="col-lg-3 col-md-4 col-sm-6 px-2 mb-4" key={product.id}>
                                                <div className="card product-card border-0">
                                                    {isAuthenticated &&
                                                        <React.Fragment>
                                                            {product.cart_count &&
                                                                <button className="btn-action-view btn-sm" data-bs-toggle="tooltip" data-bs-placement="left" title={`${product.cart_count}- In your cart`}>
                                                                    <i className="bi bi-cart" />
                                                                </button>
                                                            }
                                                        </React.Fragment>
                                                    }
                                                    <Link to={`/shop/product/${product.slug}/`} className="card-img-top overflow-hidden" >
                                                        <img src={`${product.image}`} alt="Product" />
                                                    </Link>
                                                    <div className="card-body py-2">
                                                        <span className="product-meta d-block fs-xs pb-1" >{product.category}</span>
                                                        <h3 className="product-title">
                                                            <Link to={`/shop/product/${product.slug}/`}>{product.title}</Link>
                                                        </h3>
                                                        <div className="d-flex justify-content-between">
                                                            <div className="product-price">
                                                                {product.discounted_price > 0 && <span >${product.discounted_price}</span>}
                                                                {product.discounted_price > 0 && <del className=" text-muted ms-2">${product.price}</del>}
                                                                {product.discounted_price === 0 && <span >${product.price}</span>}
                                                            </div>
                                                            <div className="star-rating">
                                                                <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                <i className="star-rating-icon bi bi-star-fill"></i>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="card-body card-body-hidden">
                                                        {isAuthenticated &&
                                                            <div className="d-flex mb-2 flex-column">
                                                                {
                                                                    product.stock > 0 ?
                                                                        <button className="btn btn-outline-primary btn-sm" onClick={
                                                                            e => APICall(Api.addToCart(product.slug), response => {
                                                                                if (response.data.item) {
                                                                                    this.setState({ cached_products: response.data.items })
                                                                                    localCartUpdated(response.data.order);
                                                                                    localHomeProductsUpdate(response.data.items);
                                                                                }
                                                                            })
                                                                        }>
                                                                            <i className="bi bi-cart-plus-fill" /> Add to Cart
                                                                        </button>
                                                                        :
                                                                        <button className="btn btn-sm btn-secondary" disabled>Out of stcok</button>
                                                                }
                                                            </div>
                                                        }
                                                        <div className="text-center">
                                                            <Link to={`/shop/product/${product.slug}/`} className='btn btn-sm btn-light'>
                                                                <i className="bi bi-eye me-1" />View
                                                            </Link>
                                                        </div>
                                                    </div>
                                                </div>
                                                <hr className="d-sm-none" />
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <div className="text-center">
                                <div className="container">
                                    <Link to="/shop/products/" className="btn btn-outline-accent px-lg-4">
                                        More
                                        <i className='ri-arrow-right-s-line ms-2 fs-5' />
                                    </Link>
                                </div>
                            </div>
                        </React.Fragment>
                    }
                </div>
            </section>
        )
    }
}



export default HomeProducts;