import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import Api from "../../Api";


const initialState = {
     homeProductsLoader: false,
     homeProductsError: null,
     homeProducts: null,
     homeProductsData: null
}

export const homeProducts = createAsyncThunk("homeProducts/trending", async () => {
     const response = await Api.homeProducts();
     return response.data;
})

export const homeProductsSlice = createSlice({
     name: "auth",
     initialState,
     reducers: {
          homeProductsUpdated: (state, action) => {
               if (!state.homeProductsLoader) {
                    state.homeProductsLoader = false
                    state.homeProductsError = null
                    state.homeProducts = action.payload
                    state.homeProductsData = action
               }
          }
     },
     extraReducers(builder) {
          builder.addCase(homeProducts.pending, (state, action) => {
               state.homeProductsLoader = true
               state.homeProductsError = null
               state.homeProducts = null
          }).addCase(homeProducts.fulfilled, (state, action) => {
               state.homeProductsLoader = false
               state.homeProductsError = null
               state.homeProducts = action.payload
               state.homeProductsData = action
          }).addCase(homeProducts.rejected, (state, action) => {
               state.homeProductsLoader = false
               state.homeProductsError = action.error.message
               state.homeProducts = null
               state.homeProductsData = action
          })
     }
})


export const { homeProductsUpdated } = homeProductsSlice.actions;
export default homeProductsSlice.reducer;