import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Link, useNavigate } from "react-router-dom";
import * as authActions from '../../redux/auth/AuthLoginReducer';

import CsrfToken from '../../redux/CsrfToken';
import GoogleSocialLogin from './socialAuth/GoogleSocialLogin';
/**
 * @mwangihub
 * @summary SignUp Component. Holds Social auth methods
 * @param   {object} this.props  received props from redux=> reduxMapper
 * @return {JSX} returns SignUp form
 */
const Signup = () => {
    const [inputType, setInputType] = useState(true)
    const [error, setError] = useState(null)
    const navigate = useNavigate()
    const authLoader = useSelector(state => state.authentication.authLoader);
    const authError = useSelector(state => state.authentication.authError);
    const authInfo = useSelector(state => state.authentication.authInfo);
    const isAuthenticated = useSelector(state => state.authentication.isAuthenticated);
    const dispatch = useDispatch();

    useEffect(() => {
        if (authError) setError(authError);
        else setError(null);
    }, [authError, navigate])
    const handleSubmit = e => {
        e.preventDefault()
        const { email, password1, password2, buyer, employee, csrfmiddlewaretoken } = e.target;
        if (password1.value !== password2.value) {
            setError("Password did not match");
            return
        }
        const object = {
            email: email.value,
            password1: password1.value,
            password2: password2.value,
            buyer: buyer.checked,
            employee: employee.checked,
            token: csrfmiddlewaretoken.value
        }
        dispatch(authActions.authSignUp(object))
    }
    const is_str = typeof error === 'string';
    const is_obj = typeof error === 'object';
    return (
        <React.Fragment>
            <div className="modal-dialog">
                <div className="modal-content rounded-5 shadow">
                    <div className="modal-header p-5 pb-4 border-bottom-0 d-flex justify-content-center">
                        <h4 className="fw-bold mb-0">Sign up for free</h4>
                    </div>
                    <div className="modal-body px-lg-5">
                        <form className="" id="signUpForm" onSubmit={e => handleSubmit(e)}>
                            {
                                error &&
                                <div className={`alert alert-danger pb-2 pt-2 ${is_str ? " text-center" : ""}`}>
                                    {is_str && error}
                                    {is_obj && Object.keys(error).map((key, index) => {
                                        return (
                                            <dl className="row" key={index}>
                                                <dt className="col-sm-3 text-capitalize">{key}</dt>
                                                <dd className="col-sm-9">
                                                    {error[key].map((e, index) => { return <p key={index}>{e}</p> })}
                                                </dd>
                                            </dl>
                                        );
                                    })
                                    }
                                </div>
                            }
                            {authInfo &&
                                <div className={`alert alert-success text-center`}>{authInfo}</div>
                            }
                            <CsrfToken />
                            <div className="did-floating-label-content">
                                <input className=" did-floating-input" type="email" placeholder="" name="email" required />
                                <label className="did-floating-label">Email address</label>
                            </div>
                            <div className="did-floating-label-content">
                                <input className=" did-floating-input" type={inputType ? "password" : "text"} placeholder="" name="password1" />
                                <label className="did-floating-label">Password</label>

                            </div>
                            <div className="did-floating-label-content">
                                <input className=" did-floating-input" type={inputType ? "password" : "text"} placeholder="" name="password2" />
                                <label className="did-floating-label">Password again</label>
                            </div>
                            <button type="button" className={`btn btn-sm btn-light mb-3 ${inputType ? "text-primary" : "text-danger"}`} onClick={e => setInputType(!inputType)} >
                                <span className="ms-2">{inputType ? "View password" : "Hide password"}</span>
                            </button>
                            <div className="form-switch-label">
                                <label className="switch">
                                    <input type="checkbox" name='buyer' defaultChecked />
                                    <span className="slider"></span>
                                </label>
                                <small className="label">To carry out actions related to buying items</small>
                            </div>
                            <div className="form-switch-label">
                                <label className="switch">
                                    <input type="checkbox" name='employee' />
                                    <span className="slider"></span>
                                </label>
                                <small className="label">To carry out actions related to job application</small>
                            </div>

                            <button className="w-100 mb-2 btn rounded-4 btn-primary" type='submit' disabled={authLoader}>
                                {authLoader ? <div className="spinner-border text-primary" /> : "Sign up"}

                            </button>
                            <small className="text-muted">By clicking Sign up, you agree to the terms of use.</small>
                            <div className=''>
                                <hr className="my-4" />
                                <h2 className="fs-6 fw-bold mb-3">Or use a third-party</h2>
                                <GoogleSocialLogin />

                            </div>
                        </form>
                    </div>

                    <div className="modal-footer border-top-0">
                        <ul className="nav justify-content-between w-100">
                            <li className="nav-item">
                                <Link to="/" className="nav-link fs-7 text-black-50 fw-bold">Innovest shop</Link>
                            </li>
                            <ul className="nav justify-content-end">
                                {!isAuthenticated &&
                                    <li className="nav-item">
                                        <Link to="/auth/login" className="nav-link fs-7 text-black-50">Sign in</Link>
                                    </li>
                                }
                                <li className="nav-item">
                                    <Link className="nav-link fs-7 text-black-50" to="/legal/privacy">Privacy</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link fs-7 text-black-50" to="/legal/terms">Terms</Link>
                                </li>
                            </ul>
                        </ul>
                    </div>
                </div>
            </div>



        </React.Fragment >
    )
}
export default Signup;
