import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Api, { APICall } from '../../Api';
import redux from "../../ReduxMapper";
import { connect } from 'react-redux';
import Main from '../../Main';

class Dashboard extends Component {


   state = {
      loading: false,
      success: null,
      error: null,
      data: null
   }
   retrieveOrders = () => {
      this.setState({ loading: true })
      APICall(Api.completedOrder(), success => {
         this.props.localProfileOrdersUpdated(success.data)
         this.setState({ loading: false })
      }, error => {
         this.setState({ loading: false })
         console.log(error);
      }, "retrieving previous orders.")
   }
   componentDidMount() {
      this.retrieveOrders()
   }
   render() {
      const { loading } = this.state;
      const { profileData: { profileOrder } } = this.props;
      var completedOders = [], beingDeliveredOrders = [], receivedOrders = [], refundReOrders = [], refundedOrders = [], norder;
      if (profileOrder) {
         profileOrder.forEach(order => {
            order.ordered ? completedOders.unshift(order) :
               (order.being_delivered) ? beingDeliveredOrders.unshift(order) :
                  order.received ? receivedOrders.unshift(order) :
                     order.refund_requested ? refundReOrders.unshift(order) :
                        order.refund_granted ? refundedOrders.unshift(order) : norder = order
         });
      }
      return (
         <React.Fragment>
            <div className="dashboard__titile">
               <h1>Dashboard</h1>
               <nav>
                  <ol className="breadcrumb">
                     <li className="breadcrumb-item"><Link to="/">Home</Link></li>
                     <li className="breadcrumb-item active"></li>
                  </ol>
               </nav>
            </div>





            <div className="section dashboard__page">
               <div className="row ">
                  {!loading && profileOrder && profileOrder.length > 0 &&
                     <div className="col-lg-12">
                        {/* All orders with status */}
                        <div className="dashboard__card px-2 ">
                           <div className="dashboard__card-body">

                              <h5 className="dashboard__card-title">All orders <span>| Orders with status </span></h5>

                              <div className="accordion" id="allOrdersWithStatus">
                                 <div className="accordion-item">
                                    <h2 className="accordion-header">
                                       <button className="accordion-button collapsed text-primary" data-bs-target="#listOfAllOrders" data-bs-toggle="collapse">
                                          View All orders
                                       </button>
                                    </h2>
                                    <div id="listOfAllOrders" className="accordion-collapse collapse" data-bs-parent="#allOrdersWithStatus">
                                       <div className="accordion-body">
                                          <div className="card card-body bg-primary bg-opacity-10 p-2 rounded-0 border-0 mb-1 fw-bold d-none d-md-none d-lg-block d-xl-block d-xxl-block">
                                             <div className="row pricing-item">
                                                <div className="col-lg-3 col-md-3 d-flex  my-1">
                                                   Refference code
                                                </div>
                                                <div className="col-lg-3 col-md-3 d-flex justify-content-center my-1">
                                                   Date created
                                                </div>
                                                <div className="col-lg-3 col-md-3 d-flex justify-content-center my-1">
                                                   Order total
                                                </div>
                                                <div className="col-lg-3 col-md-3 d-flex justify-content-center my-1">
                                                   Order status
                                                </div>
                                             </div>
                                          </div>
                                          {profileOrder.map(order => {
                                             return (
                                                <div className="card card-body overflow-hidden border-0 p-2 mb-1" key={order.id}>
                                                   <div className="row pricing-item">
                                                      <div className="col-lg-3 col-md-3 d-flex">
                                                         <small className='fw-bold d-block d-lg-none d-xl-none me-1'>Ref: </small><small>{order.ref_code}</small>
                                                      </div>
                                                      <div className="col-lg-3 col-md-4 d-flex justify-content-center">
                                                         <small className='fw-bold d-block d-lg-none d-xl-none me-1'>Created date: </small><small>{new Date(order.start_date).toDateString()}</small>
                                                      </div>
                                                      <div className="col-lg-3 col-md-2 d-flex justify-content-center">
                                                         <small className='fw-bold d-block d-lg-none d-xl-none me-1'>Total: </small><small>${order.order_total.toFixed(2)}</small>
                                                      </div>
                                                      <div className="col-lg-3 col-md-3 d-flex justify-content-center">
                                                         <small className='fw-bold d-block d-lg-none d-xl-none me-1'>Status: </small>
                                                         {
                                                            (order.ordered && !order.being_delivered) ? <span className="badge bg-primary">Completed</span> :
                                                               (!order.ordered) ? <span className="badge bg-warning">Pending</span> :
                                                                  (order.being_delivered && order.received) ? <span className="badge bg-success">Delivered</span> :
                                                                     (order.ordered && order.refund_requested) ? <span className="badge bg-secondary">Refunding</span> : null
                                                         }

                                                      </div>
                                                   </div>
                                                </div>
                                             )
                                          })}

                                       </div>
                                    </div>
                                 </div>
                              </div>
                           </div>
                        </div>
                     </div>
                  }


                  <div className="col-lg-12 mt-3">
                     {/* Completed orders large devices */}
                     <div className="col-12">
                        <div className="dashboard__card recent__orders shadow-sm">
                           {loading &&
                              <div className="d-flex justify-content-center align-items-center py-5">
                                 <div className="spinner-border text-primary" />
                              </div>
                           }

                           {completedOders.length < 1 &&
                              <div className="dashboard__card-body pb-0" >
                                 <h5 className="dashboard__card-title">You have not placed any orders so far</h5>
                                 <table className="table table-borderless">
                                    <thead className='bg-primary bg-opacity-25' data-order="details">
                                       <tr><td>Your orders will appear here </td></tr>
                                    </thead>
                                 </table >
                              </div>
                           }

                           {!loading && completedOders.length > 0 &&
                              <React.Fragment>
                                 <div className="filter">
                                    <span className="icon dropdown" onClick={e => Main.toggleFilter(e)}>
                                       <i className="bi bi-three-dots"></i>
                                    </span>
                                    <ul className="dropdown-menu dropdown-menu-end dropdown-menu-arrow">
                                       <li className="dropdown-header text-start">
                                          <h6>Filter</h6>
                                       </li>
                                       <li><a className="dropdown-item" href="/">Today</a></li>
                                       <li><a className="dropdown-item" href="/">This Month</a></li>
                                       <li><a className="dropdown-item" href="/">This Year</a></li>
                                    </ul>
                                 </div>

                                 <div className="dashboard__card-body pb-0" >
                                    <h5 className="dashboard__card-title">Completed orders <span>| Completed</span></h5>
                                    {completedOders.map(order =>
                                       <table className="table table-borderless" key={order.id} data-table={`${order.ref_code}`}>
                                          <thead className='bg-primary bg-opacity-25' data-order="details">
                                             <tr>
                                                <th colSpan={1}>
                                                   <div className="d-flex">
                                                      <span className='d-md-none d-lg-block d-none'>Ref:</span>
                                                      <Link to="/" className='text-primary ms-2' id={`${order.ref_code}`}
                                                         onClick={e => Main.toggleOrders(e)}>{order.ref_code}</Link>
                                                   </div>
                                                </th>
                                                <th colSpan={1}>
                                                   <div className="d-flex">
                                                      <span className='d-md-none d-lg-block d-none'>Date:</span>
                                                      <span className='text-muted ms-2'>{new Date(order.start_date).toDateString()}</span>
                                                   </div>
                                                </th>
                                                <th colSpan={2}>
                                                   <div className="d-flex">
                                                      <span className='d-md-none d-lg-block d-none'>Total:</span>
                                                      <span className="text-muted ms-2">${(order.order_total.toFixed(2))}</span>
                                                   </div>
                                                </th>
                                                <th colSpan={1}>
                                                   <div className="d-flex">
                                                      <span className='d-md-none d-lg-block d-none'> Shipped to:</span>
                                                      <span className="text-muted ms-2">{order.address.address}</span>
                                                   </div>
                                                </th>
                                             </tr>
                                          </thead>
                                          <thead className='bg-primary bg-opacity-10' data-products="products">
                                             <tr>
                                                <th scope="col">Products</th>
                                                <th scope="col">Title</th>
                                                <th scope="col">Price</th>
                                                <th scope="col">Qty</th>
                                                <th scope="col">Total</th>
                                             </tr>
                                          </thead >
                                          <tbody data-products="details">
                                             {order.items.map(item =>
                                                <tr key={item.id}>
                                                   {/* ${localStorage.getItem("host")} */}
                                                   <th scope="row">
                                                      <img src={`${item.item.image}`} alt="item" />
                                                   </th>
                                                   <td className='text-primary fw-bold'>
                                                      {(item.item.title).split(" ").splice(0, 7).join(" ")} ...
                                                   </td>
                                                   <td>
                                                      ${item.item.discounted_price > 0 ? (item.item.discounted_price).toFixed(2) : (item.item.price).toFixed(2)}
                                                      {item.item.discounted_price > 0 && <span className="ms-1 badge bg-success">Discount -${(item.amnt_saved).toFixed(2)}</span>}
                                                   </td>
                                                   <td className="fw-bold">{item.quantity}</td>
                                                   <td>${(item.total).toFixed(2)}</td>
                                                </tr>
                                             )}
                                          </tbody>
                                       </table>
                                    )}
                                 </div>


                              </React.Fragment>
                           }
                        </div>
                     </div>
                  </div>

                  {!loading && completedOders.length > 0 &&
                     <>
                        <div className="col-lg-12 mb-3">
                           {/* Completed order Small devices */}
                           <div className="dashboard__card recent__orders__sm shadow-sm ">
                              <div className="dashboard__card-body px-2" >
                                 <h5 className="dashboard__card-title">Completed orders <span>| Completed</span></h5>
                                 {completedOders.map(order => {
                                    return (
                                       <React.Fragment key={order.id}>
                                          <ul className="list-group list-group-horizontal list-unstyled">
                                             <li className="list-group-item bg-primary bg-opacity-25 w-50">Ref code:</li>
                                             <li className="list-group-item bg-primary bg-opacity-25  w-50">{order.ref_code}</li>
                                          </ul>
                                          <ul className="list-group list-group-horizontal list-unstyled">
                                             <li className="list-group-item bg-primary bg-opacity-25 w-50">Date:</li>
                                             <li className="list-group-item bg-primary bg-opacity-25 text-primary w-50">02/09/2022</li>
                                          </ul>
                                          <ul className="list-group list-group-horizontal list-unstyled">
                                             <li className="list-group-item bg-primary bg-opacity-25 w-50">Total:</li>
                                             <li className="list-group-item bg-primary bg-opacity-25 text-primary w-50">${(order.order_total).toFixed(2)}</li>
                                          </ul>
                                          <ul className="list-group list-group-horizontal list-unstyled">
                                             <li className="list-group-item bg-primary bg-opacity-25 w-50">Shipped to:</li>
                                             <li className="list-group-item bg-primary bg-opacity-25 text-primary w-50">{order.address.address}</li>
                                          </ul>

                                          <div className="d-sm-flex justify-content-between align-items-center py-2 border-bottom">
                                             <div className="d-block d-sm-flex text-sm-start">
                                                {order.items.map(item =>
                                                   <React.Fragment key={item.id}>
                                                      <div className="text-center">
                                                         <div className="d-inline-block flex-shrink-0 mx-auto me-sm-4">
                                                            <img src={`${item.item.image}`} alt="item" width="100" />
                                                         </div>
                                                      </div>
                                                      <div className="pb-2">
                                                         <h3 className="fw-bold fs-7 mb-2 text-primary">
                                                            {item.item.title}
                                                         </h3>
                                                         <div className="fs-7">
                                                            <span className="text-muted me-2">Price:</span>
                                                            ${item.item.discounted_price > 0 ? (item.item.discounted_price).toFixed(2) : (item.item.price).toFixed(2)}
                                                            {item.item.discounted_price > 0 && <span className="ms-2 badge bg-success">On Discount -${(item.amnt_saved).toFixed(2)}</span>}
                                                         </div>
                                                         <div className="fs-7"><span className="text-muted me-3">Quantity:</span>{item.quantity}</div>
                                                         <div className="fs-7">
                                                            <span className="text-muted me-2">Total:</span> ${(item.total).toFixed(2)}
                                                         </div>
                                                      </div>
                                                   </React.Fragment>
                                                )}
                                             </div>
                                          </div>
                                       </React.Fragment>
                                    )
                                 })}
                              </div>
                           </div>
                        </div>


                        <div className="col-lg-12">
                           {/* Order tracking */}
                           <div className="dashboard__card mb-4">
                              <div className="dashboard__card-body">
                                 <h5 className="dashboard__card-title">Order tracking <span>| Undelivered </span></h5>

                                 <div className="accordion" id="trackCompletedOrders">
                                    {completedOders.map(order => {
                                       return (
                                          <div className="accordion-item" key={order.id}>
                                             <h4 className="accordion-header ">
                                                <button className="accordion-button fs-7 collapsed text-primary" data-bs-target="#listOfOrders" type="button" data-bs-toggle="collapse">
                                                   Ref: {order.ref_code}
                                                </button>
                                             </h4>
                                             <div id="listOfOrders" className="accordion-collapse collapse" data-bs-parent="#trackCompletedOrders">
                                                <div className="accordion-body">
                                                   <div className="row">
                                                      <div className="col-xxl-3 col-md-6">
                                                         <div className="dashboard__card info-card">

                                                            <div className="dashboard__card-body">
                                                               <h5 className="dashboard__card-title">First <span>| order placed</span></h5>
                                                               <div className="d-flex align-items-center">
                                                                  <div className="card-icon rounded-circle d-flex align-items-center justify-content-center bg-primary bg-opacity-25">
                                                                     <i className="bi bi-bag fs-4 text-primary" />
                                                                  </div>
                                                                  <div className="ps-3">
                                                                     <span className="text-success small pt-1 fw-bold">100%</span>
                                                                     <span className="text-muted small pt-2 ps-1">Order placed</span>
                                                                  </div>
                                                               </div>
                                                            </div>
                                                         </div>
                                                      </div>


                                                      <div className="col-xxl-3 col-md-6">
                                                         <div className="dashboard__card info-card">
                                                            <div className="dashboard__card-body">
                                                               <h5 className="dashboard__card-title">Second <span>| Processing order</span></h5>
                                                               <div className="d-flex align-items-center">
                                                                  <div className={`card-icon rounded-circle d-flex align-items-center justify-content-center  bg-opacity-25 ${(order.ordered && !order.being_delivered) ?
                                                                     'bg-danger' : (order.ordered && order.being_delivered) ? "bg-primary" : "bg-primary"}`}>
                                                                     <i className={`bi bi-gear-fill fs-4 ${(order.ordered && !order.being_delivered) ?
                                                                        'text-danger' : (order.ordered && order.being_delivered) ? "text-primary" : "text-primary"}`} />
                                                                  </div>
                                                                  <div className="ps-3">
                                                                     {(order.ordered && !order.being_delivered) ?
                                                                        <>
                                                                           <div><span className="badge bg-danger">Current</span></div>
                                                                           <span className="text-muted small pt-2 ps-1">Currently checking the order quality check</span>
                                                                        </>
                                                                        : (order.ordered && order.being_delivered) ?
                                                                           <>
                                                                              <span className="text-success small pt-1 fw-bold">100%</span>
                                                                              <span className="text-muted small pt-2 ps-1">Quality approved</span>
                                                                           </>
                                                                           : null
                                                                     }
                                                                  </div>
                                                               </div>
                                                            </div>
                                                         </div>
                                                      </div>


                                                      <div className="col-xxl-3 col-md-6">
                                                         <div className="dashboard__card info-card customers-card">
                                                            <div className="dashboard__card-body">
                                                               <h5 className="dashboard__card-title">Third <span>| Order dispatched</span></h5>
                                                               <div className="d-flex align-items-center">
                                                                  <div className={`card-icon rounded-circle d-flex align-items-center justify-content-center  bg-opacity-25 ${(order.being_delivered && !order.received) ?
                                                                     'bg-danger' : (order.being_delivered && order.received) ? 'bg-primary' : 'bg-primary'}`}>
                                                                     <i className={`bi bi-box  fs-4 ${(order.being_delivered && !order.received) ?
                                                                        'text-danger' : (order.being_delivered && order.received) ? 'text-primary' : 'text-primary'}`}></i>
                                                                  </div>
                                                                  <div className="ps-3">
                                                                     {(order.being_delivered && !order.received) ?
                                                                        <>
                                                                           <div><span className="badge bg-danger">Current</span></div>
                                                                           <span className="text-muted small pt-2 ps-1">Order is dispached to its address</span>
                                                                        </>
                                                                        : (order.being_delivered && order.received) ?
                                                                           <>
                                                                              <span className="text-success small pt-1 fw-bold">100%</span>
                                                                              <span className="text-muted small pt-2 ps-1">Completed dispatch </span>
                                                                           </>
                                                                           :
                                                                           <>
                                                                              <span className="text-success small pt-1 fw-bold">0%</span>
                                                                              <span className="text-muted small pt-2 ps-1">Awaiting Processing </span>
                                                                           </>
                                                                     }
                                                                  </div>
                                                               </div>
                                                            </div>
                                                         </div>
                                                      </div>

                                                      <div className="col-xxl-3 col-md-6">
                                                         <div className="dashboard__card info-card customers-card">
                                                            <div className="dashboard__card-body">
                                                               <h5 className="dashboard__card-title">Fourth <span>| Order received</span></h5>
                                                               <div className="d-flex align-items-center">
                                                                  <div className={`card-icon rounded-circle d-flex align-items-center justify-content-center  bg-opacity-25 ${(order.received) ? 'bg-success' : 'bg-primary'}`}>
                                                                     <i className={`bi bi-house-door fs-4 ${(order.received) ? 'text-success' : 'text-primary'}`}></i>
                                                                  </div>
                                                                  <div className="ps-3">
                                                                     {(order.received) ?
                                                                        <>
                                                                           <span className="text-success small pt-1 fw-bold">100%</span>
                                                                           <span className="text-muted small pt-2 ps-1">Order received</span>
                                                                        </>
                                                                        :
                                                                        <>
                                                                           <span className="text-success small pt-1 fw-bold">0%</span>
                                                                           <span className="text-muted small pt-2 ps-1">Awaiting dispatch </span>
                                                                        </>
                                                                     }
                                                                  </div>
                                                               </div>
                                                            </div>
                                                         </div>
                                                      </div>

                                                   </div>
                                                </div>
                                             </div>
                                          </div>
                                       )
                                    }
                                    )}
                                 </div>
                              </div>
                           </div>


                        </div>
                     </>
                  }
               </div>
            </div>
         </React.Fragment >
      )
   }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Dashboard);