import React from 'react'
import { connect } from 'react-redux'

export const Address = ({ id, address, first_name, last_name, phone, crudMethod, _default, update_success, update_error, update_load }) => {
    return (
        <div className="col-lg-12 mb-2">
            <div className="card px-2 border-2 border-primary">
                <div className="row pricing-item my-3">

                    <div className="col-lg-3 col-md-6 d-flex align-items-center justify-content-center my-lg-0 my-md-3 my-2">
                        <div className="info-box dashboard__card w-100 mb-0 p-3">
                            <i className="bi bi-person-fill fs-4"></i>
                            <h3 className='my-1'>Names</h3>
                            <p>{first_name} {last_name}</p>
                        </div>

                    </div>
                    <div className="col-lg-3 col-md-6 d-flex align-items-center justify-content-center my-lg-0 my-md-3 my-2">

                        <div className="info-box dashboard__card w-100 mb-0 p-3">
                            <i className="bi bi-geo-fill fs-4"></i>
                            <h3 className='my-1'>Address</h3>
                            <p>{address}</p>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-6 d-flex align-items-center justify-content-center my-lg-0 my-md-3 my-2">
                        <div className="info-box dashboard__card w-100 mb-0 p-3">
                            <i className="bi bi-phone-fill fs-4"></i>
                            <h3 className='my-1'>Contact</h3>
                            <p>{phone}</p>
                        </div>

                    </div>
                    <div className="col-lg-3 col-md-6 d-flex align-items-start justify-content-between flex-column my-lg-0 my-md-3 my-2">
                        <div className="text-center mb-1 ">
                            <div className="form-check form-switch">
                                <input className="form-check-input" type="checkbox" data-crud="set-default" onChange={e => crudMethod(id, e)} disabled={update_load} id="outlined" />
                                <label className="form-check-label" htmlFor='outlined'>{_default ? "Default" : "Set to default"} {_default && <i className="bi bi-check-circle-fill ms-1 text-success" ></i>} </label>
                            </div>

                        </div>
                        <div className="text-center mb-1">
                            <span className="badge bg-danger pt-2 w-100 cursor-p" onClick={e => {
                                if (!update_load) {
                                    crudMethod(id, e)
                                }
                            }} data-crud="delete">
                                <i className="bi bi-trash"></i> Delete address
                            </span>
                        </div>
                        <div className="text-center d-inline">
                            <span className="badge bg-primary pt-2 w-100 cursor-p" data-bs-toggle="collapse" data-bs-target={`#editAdressForm${id}`}>
                                <i className="bi bi-pen"></i> Update address<i className="bi bi-check-circle-fill ms-1 text-white fs-4 d-none" id='checked-updated' ></i>
                            </span>


                        </div>
                    </div>

                    <div className="col-lg-12">
                        <div className="accordion" >
                            <div className="accordion-item border-0">
                                <div id={`editAdressForm${id}`} className="accordion-collapse collapse">
                                    <div className="accordion-body">
                                        <div className="d-flex justify-content-center">
                                            <div className="col-lg-8">
                                                <form className='billingForm' onSubmit={event => {
                                                    event.preventDefault();
                                                    if (!update_load) {
                                                        crudMethod(id, event)
                                                    }
                                                }} id="update_form">
                                                    <div>
                                                        <div className="row g-3">

                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='first_name' placeholder="" required />
                                                                    <label className="did-floating-label">First name</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='last_name' placeholder="" required />
                                                                    <label className="did-floating-label">Second name</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className="did-floating-input" type="text" name='phone' placeholder="" required />
                                                                    <label className="did-floating-label">Phone number (+123 ... )</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='address' placeholder="" required />
                                                                    <label className="did-floating-label">Address /Town</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-12">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='state' placeholder="" required />
                                                                    <label className="did-floating-label">ZIP code /County</label>
                                                                </div>
                                                            </div>
                                                        </div>


                                                        <div className="ps-2 d-flex justify-content-end">
                                                            <button type='submit' className="btn btn-success">
                                                                <span className="d-none d-sm-inline">Update shipping address</span>
                                                            </button>
                                                        </div>
                                                    </div>

                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

        </div>
    )
}

const mapStateToProps = (state) => ({})

const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(Address)