import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import Api from "../../Api";

const initialState = {
    authLoader: false,
    authError: null,
    authData: null,
    isAuthenticated: true,
    profile: null,
    authInfo: null
}
export const authWithGoogle = createAsyncThunk("auth/google", async (object) => {
    const response = await Api.authWithGoogle(object);
    return response.data;
})
export const login = createAsyncThunk("auth/login", async (object) => {
    const { email, password, csrfmiddlewaretoken } = object;
    const response = await Api.login(email, password, csrfmiddlewaretoken);
    return response.data;
})
export const authSignUp = createAsyncThunk("auth/signup", async (object) => {
    const response = await Api.authSignUp(object);
    return response.data;
})
export const logout = createAsyncThunk("auth/Logout", async () => {
    const response = await Api.logOut();
    return response.data;
})
export const checkAuthStatus = createAsyncThunk("auth/checkauth", async () => {
    try {
        const response = await Api.checkAuth();
        return response.data;
    } catch (error) {
        return error.message;
    }
})


export const authLogin = createSlice({
    name: "auth",
    initialState,
    reducers: {
        localAuthenticated: (state, action) => {
            if (!state.authLoader) {
                state.authData = action
                state.authError = null
                state.authInfo = action.payload
                state.isAuthenticated = true
            }
        },
        localAuthFail: (state, action) => {
            state.authData = action.payload
            state.authError = "Google sigin canceled."
            state.authInfo = action.payload
            state.isAuthenticated = false
            state.profile = null
        },
    },
    extraReducers(builder) {
        builder
            .addCase(authWithGoogle.pending, (state, action) => {
                state.authLoader = true
                state.authError = null
                state.authData = null
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = state.profile ? state.profile : null
            }).addCase(authWithGoogle.fulfilled, (state, action) => {
                console.log(action.payload)
                state.authLoader = false
                state.authError = null
                state.authInfo = action.payload
                state.authData = action
                state.isAuthenticated = true
                state.profile = null //action.payload
                state.authLoader = false
            }).addCase(authWithGoogle.rejected, (state, action) => {
                state.authLoader = false
                state.authError = //action.error.message
                    state.authData = action
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = state.profile ? state.profile : null
            })


            .addCase(login.pending, (state, action) => {
                state.authLoader = true
                state.authError = null
                state.authData = null
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = state.profile ? state.profile : null
            }).addCase(login.fulfilled, (state, action) => {
                state.authLoader = false
                if (!action.payload.success) {
                    state.authError = action.payload.details.info
                    state.authInfo = null
                } else {
                    state.authError = null
                    state.authInfo = action.payload.details
                }
                state.authData = action
                state.isAuthenticated = action.payload.isAuthenticated
                state.profile = action.payload.profile
                state.authLoader = false
            }).addCase(login.rejected, (state, action) => {
                state.authLoader = false
                state.authError = action.error.message
                state.authData = action
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = state.profile ? state.profile : null
            })

            .addCase(logout.pending, (state, action) => {
                state.authLoader = true
                state.authError = null
                state.authData = null
                state.isAuthenticated = true
                state.authInfo = null
                state.profile = state.profile ? state.profile : null
            }).addCase(logout.fulfilled, (state, action) => {
                state.authLoader = false
                state.authError = null
                state.authInfo = null
                state.authData = action
                state.isAuthenticated = action.payload.isAuthenticated
                state.profile = null
            }).addCase(logout.rejected, (state, action) => {
                state.authLoader = false
                state.authError = action.error.message
                state.authData = action
                state.authInfo = null
                state.isAuthenticated = true
                state.profile = state.profile ? state.profile : null
            })

            .addCase(checkAuthStatus.pending, (state, action) => {
                state.authLoader = true
                state.authError = null
                state.authData = null
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = state.profile ? state.profile : null
            }).addCase(checkAuthStatus.fulfilled, (state, action) => {
                state.authLoader = false
                state.authError = null
                state.authInfo = null
                state.authData = action
                state.isAuthenticated = action.payload.isAuthenticated
                state.profile = action.payload.profile
            }).addCase(checkAuthStatus.rejected, (state, action) => {
                state.authLoader = false
                state.authError = action.error.message
                state.authData = action
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = state.profile ? state.profile : null
            })

            .addCase(authSignUp.pending, (state, action) => {
                state.authLoader = true
                state.authError = null
                state.authData = null
                state.authInfo = null
                state.isAuthenticated = false
                state.profile = null
            }).addCase(authSignUp.fulfilled, (state, action) => {
                state.authLoader = false
                if (!action.payload.success) {
                    state.authError = action.payload.details.info
                    state.authInfo = null
                } else {
                    state.authError = null
                    state.authInfo = action.payload.details
                }
                state.authData = action
                state.isAuthenticated = action.payload.isAuthenticated
                state.profile = action.payload.profile
            }).addCase(authSignUp.rejected, (state, action) => {
                state.authLoader = false
                state.authError = action.error.message
                state.authData = action
                state.isAuthenticated = false
                state.profile = null
                state.authInfo = null
            })

    }
})


export const { localAuthenticated, localAuthFail } = authLogin.actions;
export default authLogin.reducer;