import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const NotFound = props => {
  const [loading, setLoading] = useState(true)
  const [link, setLink] = useState("/")
  const [linkTo, setlinkTo] = useState(null)
  useEffect(() => {
    let header = document.querySelector("#header");
    if (header) {
      header.remove();
    }
    setTimeout(() => {
      setLoading(false)
    }, 1000);
  }, [])
  useEffect(() => {
    if (props.link) {
      setLink(props.link)
      setlinkTo(props.to)
    }
  }, [props.link])

  return (
    <>
      <div className="container my-5 mx-auto py-5">
        <div className="bg-light p-5 my-5 rounded">
          <h1>404 Not found</h1>
          <p className="lead">This example is a quick exercise to illustrate how fixed to top navbar works. As you scroll, it will remain fixed to the top of your browsers viewport.</p>
          <Link className="btn btn-sm btn-primary" to={link} >{`Go to ${linkTo ? linkTo : "Home"}`}</Link>
        </div>
      </div>
      {loading && <div id="preloader" />}
    </>

  )
}
export default NotFound