import React, { Component } from 'react';
import Api from '../../../Api'
/**
 * @mwangihub
 * @summary Hig Order Component that calls the same API end point
 * @param   {object} SocialLoginMethod  SOcial Login method to be returned as JSX
 * @param   {object} data Any data to be utilized by HOC, for this case name obj used to call API endpoint of that social method
 * @return {object} returns state of with details from API endpoint & any details 
 */
const SocialLogin = (SocialLoginMethod, data_) => {
    return class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                clientID: null,
                clientSecret: null,
                key: null
            }
        }
        componentDidMount() {
            Api.requestSocialAppKey(data_)
                .then(response => {
                    const { data } = response;
                    this.setState({ clientID: data.client_id, clientSecret: data.client_secret, key: data.key })
                }).catch(error => {
                    console.log(error);
                })
        }

        render() {
            return (
                <React.Fragment>
                    {this.state.clientID && < SocialLoginMethod {...this.state} {...this.props} />}
                </React.Fragment>

            )
        }
    }


}


export default SocialLogin