import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import Api from "../../Api";


const initialState = {
     profileOrderLoader: false,
     profileOrderError: null,
     profileOrder: null,
     profileOrderData: null,

     profileBuyerLoader: false,
     profileBuyerError: null,
     profileBuyerProfile: null,
     profileBuyerData: null,

}

export const completedOrders = createAsyncThunk("profile/orders", async () => {
     const response = await Api.homeProducts();
     return response.data;
})
export const profileOrder = createAsyncThunk("profile/orders", async () => {
     const response = await Api.homeProducts();
     return response.data;
})

export const profileReducer = createSlice({
     name: "profile",
     initialState,
     reducers: {
          profileOrdersUpdated: (state, action) => {
               if (!state.profileOrderLoader) {
                    state.profileOrderLoader = false
                    state.profileOrderError = null
                    state.profileOrder = action.payload
                    state.profileOrderData = action.payload
               }
          },
          profileBuyerProfileUpdated: (state, action) => {
               if (!state.profileBuyerLoader) {
                    state.profileBuyerLoader = false
                    state.profileBuyerError = null
                    state.profileBuyerData = action
                    state.profileBuyerProfile = action.payload
               }
          }
     },
     extraReducers(builder) {
          builder
               .addCase(completedOrders.pending, (state, action) => {

               }).addCase(completedOrders.fulfilled, (state, action) => {

               }).addCase(completedOrders.rejected, (state, action) => {

               })
     }
})


export const { profileOrdersUpdated, profileBuyerProfileUpdated } = profileReducer.actions;
export default profileReducer.reducer;