import * as homeActions from './redux/home/HomeProductsReducer';
import * as authActions from './redux/auth/AuthLoginReducer';
import * as cartActions from './redux/cart/CartActionsReducer';
import * as profileActions from './redux/profile/profileReducer';


const mapStateToProps = state => {
    // console.log(state.profileActions)
    return {
        products: state.homeProducts,
        authentication: state.authentication,
        cartProducts: state.cartActions,
        profileData: state.profileActions,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getHomeProducts: () => dispatch(homeActions.homeProducts()),

        localAuthenticated: object => dispatch(authActions.localAuthenticated(object)),
        localAuthFail: object => dispatch(authActions.localAuthFail(object)),
        checkAuth: () => dispatch(authActions.checkAuthStatus()),
        authLogin: object => dispatch(authActions.login(object)),
        authWithGoogle: object => dispatch(authActions.authWithGoogle(object)),
        authLogout: (func = null) => {
            console.log(func)
            if (func) {
                func();
            }
            dispatch(authActions.logout());
            dispatch(homeActions.homeProducts());
            dispatch(cartActions.orderSummary());
        },

        getCart: () => dispatch(cartActions.orderSummary()),

        localCartUpdated: object => dispatch(cartActions.cartUpdated(object)),
        localHomeProductsUpdate: object => dispatch(homeActions.homeProductsUpdated(object)),
        localProfileOrdersUpdated: object => dispatch(profileActions.profileOrdersUpdated(object)),
        localProfileBuyerUpdated: object => dispatch(profileActions.profileBuyerProfileUpdated(object)),
    }
}
const ReduxMapper = {
    mapStateToProps,
    mapDispatchToProps,
}

export default ReduxMapper