import React from 'react'
import { Link } from 'react-router-dom';


const AsideCartInfo = ({ items, coupon: { amount, code, redeemed }, order_total, total_discount, review }) => {

    return (
        <aside className="col-lg-4 pt-4 pt-lg-0 ps-xl-5 order-md-last">
            <div className="bg-white rounded-3 shadow-lg p-4 ms-lg-auto">
                <div className="py-2 px-xl-2">
                    <div className="widget mb-3">
                        <h2 className="widget-title text-center">Order summary</h2>
                        {!review &&
                            <div>
                                {
                                    items.map(item => {
                                        return (
                                            <div className="d-flex align-items-center py-2 border-bottom" key={item.id}>
                                                <Link className="d-block flex-shrink-0" to={`/shop/product/${item.item.slug}`}>
                                                    {/* <img src={`http://localhost:8000${item.item.image}`} alt="Product" width="50" /> */}
                                                    <img src={`${item.item.image}`} alt="Product" width="50" />
                                                </Link>
                                                <div className="ps-2">
                                                    <h6 className="fs-7">
                                                        <Link to={`/shop/product/${item.item.slug}`} className="text-darker" >{item.item.title}</Link>
                                                    </h6>
                                                    <div className="fs-sm">
                                                        <span className="text-accent me-2">${(item.total).toFixed(2)}</span>
                                                        <span className="text-muted">({item.quantity})</span></div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        }
                        <ul className="list-unstyled fs-sm pb-2 border-bottom">
                            <li className="d-flex justify-content-between align-items-center">
                                <span className="me-2">Subtotal:</span>
                                <span className="text-end">${(order_total).toFixed(2)}</span>
                            </li>
                            {/* <li className="d-flex justify-content-between align-items-center">
                                <span className="me-2">Shipping:</span>
                                <span className="text-end">—</span>
                            </li> */}
                            {amount &&
                                <li className="d-flex justify-content-between align-items-center">
                                    <span className="me-2">Coupon:</span>
                                    <span className="text-end">$ -{amount}</span>
                                </li>
                            }
                            <li className="d-flex justify-content-between align-items-center">
                                <span className="me-2">Discount:</span>
                                <span className="text-end">${(total_discount).toFixed(2)}</span>
                            </li>
                        </ul>
                        <h3 className="fw-normal text-center my-4">${(order_total).toFixed(2)}</h3>

                    </div>
                </div>
            </div>
        </aside>
    )
}



export default AsideCartInfo