import React, { Component } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import { connect } from 'react-redux'
import redux from '../../ReduxMapper';
import Utils from '../../utils/utils';
import CartItemRow from './CartItemRow'
import component from '../../Components';

/**
 *
 * @mwangihub
 * @summary Cart Component
 * @class Cart
 * @extends {Component} 
 * @param {object} props From redux => redux Mapper
 *      - cartItems - it the order object, either to be null or empty object or object with items in that order
 *      - @function  deleteFromCart
 *      - @function  reduceFromCart
 *      - @function  addToCart
 * @return {JSX} return JSX <CartItemRow/>  
 *
 */

class OrderReviewComponent extends Component {
    state = {}
    componentDidMount() {
        this.props.getCart()
    }
    static getDerivedStateFromProps(props, state) {
        if (props.cartProducts.cartItems) {
            try {
                if (props.cartProducts.cartItems.address.user === null)
                    props.navigate("/cart/shipping-address")
            } catch (error) {

            }
            return null;
        }
        return null;
    }
    render() {
        const { cartProducts: { cartItems } } = this.props;
        const review = true
        return (
            <React.Fragment>
                <component.BreadCrumb
                    title={"Order review"}
                    current={"Order review"}
                    links={[
                        { name: 'Home', to: '/' },
                        { name: 'Shop', to: '/shop/products/' },
                        { name: 'Cart', to: '/cart/cart-view/' },
                        { name: 'Shipping address', to: '/cart/shipping-address' },
                    ]}
                />

                {
                    !cartItems &&
                    <div className="col-lg-6 col-md-8 mx-auto my-5 py-5">
                        <h1 className="fw-light">Your cart is empty.</h1>
                        <p className="lead text-muted">Buy more items to increase your chances of being awarded free <span className="text-success fw-bold"> shopping credit. </span> Check more items in the trending section, most of them have discounts. </p>
                        <p>
                            <Link to="/shop/products" className="btn btn-primary my-2">Continue shopping</Link>
                        </p>
                    </div>
                }
                {cartItems && cartItems.items.length > 0 &&
                    <div className="container pb-5 mb-2 mb-md-4 my-5">
                        <h4 className="mb-3">Review your order</h4>
                        <div className="row mt-3">
                            <div className="col-lg-8 ">
                                {
                                    cartItems.items.map(item => {
                                        const product = Utils.objectUpdate(item, {
                                            review
                                        })
                                        return (
                                            <CartItemRow {...product} key={item.id} />
                                        )
                                    })
                                }
                                <div className="bg-light rounded-3 px-4 pt-4 pb-2">
                                    <div className="row">
                                        <div className="col-sm-6">
                                            <h4 className="h6">Shipping to:</h4>
                                            <ul className="list-unstyled fs-sm">
                                                <li><span className="text-muted me-1">Client:</span>{cartItems.address.first_name} {cartItems.address.last_name}</li>
                                                <li><span className="text-muted me-1">Address:</span>{cartItems.address.address}</li>
                                                <li><span className="text-muted me-1">Phone:</span>{cartItems.address.phone}</li>
                                            </ul>
                                        </div>
                                        <div className="col-sm-6">
                                            <h4 className="h6">Payment method:</h4>
                                            <ul className="list-unstyled fs-sm">
                                                <li><span className="text-muted me-1">Mpesa:</span>{cartItems.address.phone}</li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                                <div className=" d-flex pt-4">
                                    <div className="w-50 pe-3">
                                        <Link className="btn btn-secondary d-block w-100" to="/cart/shipping-address">
                                            <i className="bi bi-arrow-left mt-sm-0 me-1"></i>
                                            <span className="d-none d-sm-inline">Back to shipping address</span>
                                            <span className="d-inline d-sm-none">Back</span>
                                        </Link>
                                    </div>
                                    <div className="w-50 ps-2">
                                        <Link className="btn btn-warning d-block w-100" to='/cart/payment' >
                                            <span className="d-none d-sm-inline">Payment</span>
                                            <span className="d-inline d-sm-none">Payment</span>
                                            <i className="bi bi-arrow-right mt-sm-0 ms-1"></i>
                                        </Link>
                                    </div>
                                </div>
                            </div>

                            {cartItems &&
                                <aside className="col-lg-4 pt-4 pt-lg-0 ps-xl-5 order-md-last">
                                    <div className="bg-white rounded-3 shadow-lg p-4 ms-lg-auto">
                                        <div className="py-2 px-xl-2">
                                            <div className="widget mb-3">
                                                <h2 className="widget-title text-center">Order summary</h2>

                                                <ul className="list-unstyled fs-sm pb-2 border-bottom">
                                                    <li className="d-flex justify-content-between align-items-center">
                                                        <span className="me-2">Subtotal:</span>
                                                        <span className="text-end">${(cartItems.order_total).toFixed(2)}</span>
                                                    </li>

                                                    {cartItems.coupon.amount &&
                                                        <li className="d-flex justify-content-between align-items-center">
                                                            <span className="me-2">Coupon:</span>
                                                            <span className="text-end">$ -{cartItems.coupon.amount}</span>
                                                        </li>
                                                    }
                                                    <li className="d-flex justify-content-between align-items-center">
                                                        <span className="me-2">Discount:</span>
                                                        <span className="text-end">${(cartItems.total_discount).toFixed(2)}</span>
                                                    </li>
                                                </ul>
                                                <h3 className="fw-normal text-center my-4">${(cartItems.order_total).toFixed(2)}</h3>

                                            </div>
                                        </div>
                                    </div>
                                </aside>
                            }
                        </div>
                    </div>
                }

            </React.Fragment>
        )
    }
}

const OrderReview = (props) => {
    const navigate = useNavigate()
    const location = useLocation()
    const properties = {
        navigate, location
    }
    return <OrderReviewComponent {...properties} {...props} />
}


export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(OrderReview);