import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { Link } from "react-router-dom";
import * as authActions from '../../redux/auth/AuthLoginReducer';
import CsrfToken from '../../redux/CsrfToken';
import GoogleSocialLogin from './socialAuth/GoogleSocialLogin';

/**
 * @mwangihub
 * @summary Login Component 
 * @param   {object} props  received props from redux => reduxMapper
 * @return {JSX} returns Login form
 */
const Login = () => {
    const [inputType, setInputType] = useState(true)
    const navigate = useNavigate()
    const authLoader = useSelector(state => state.authentication.authLoader);
    const authError = useSelector(state => state.authentication.authError);
    const authInfo = useSelector(state => state.authentication.authInfo);
    const isAuthenticated = useSelector(state => state.authentication.isAuthenticated);
    const dispatch = useDispatch();

    const viewPassword = e => {
        const el = e.target;
        el.classList.toggle("bi-eye-slash-fill");
        setInputType(!inputType);
    }
    const handleLogin = e => {
        e.preventDefault();
        const password = e.target.password.value;
        const email = e.target.email.value;
        const csrfmiddlewaretoken = e.target.csrfmiddlewaretoken.value;
        const object = { email, password, csrfmiddlewaretoken };
        dispatch(authActions.login(object));
    }
    useEffect(() => {
        setTimeout(() => {
            if (isAuthenticated) { navigate("/"); }
        }, 1500);
    }, [isAuthenticated, navigate]);

    const is_str = typeof authError === 'string';
    const is_obj = typeof authError === 'object';
    return (
        <React.Fragment>
            <div className="modal-dialog">
                <div className="modal-content rounded-5 shadow">
                    <div className="modal-header p-5 pb-4 border-bottom-0 d-flex justify-content-center">
                        <h3 className="fw-bold mb-0">Sign in</h3>
                    </div>
                    <div className="modal-body px-lg-5">
                        <form className="" onSubmit={e => handleLogin(e)}>
                            {
                                authError &&
                                <div className={`alert alert-danger pb-2 pt-2 ${is_str ? " text-center" : ""}`}>
                                    {is_str && authError}
                                    {is_obj && Object.keys(authError).map((key, index) => {
                                        return (
                                            <dl className="row mb-0" key={index}>
                                                <dt className="col-sm-3 text-capitalize">{key}</dt>
                                                <dd className="col-sm-9">
                                                    {authError[key].map((e, index) => { return <p key={index}>{e}</p> })}
                                                </dd>
                                            </dl>
                                        );
                                    })
                                    }
                                </div>
                            }
                            {authInfo &&
                                <div className={`alert alert-success text-center`}>{authInfo}</div>
                            }
                            <CsrfToken />
                            <div className="did-floating-label-content">
                                <input className=" did-floating-input" type="email" placeholder="" name="email" required />
                                <label className="did-floating-label">Email address</label>
                            </div>

                            <div className="did-floating-label-content input-groups">
                                <span className="input-group-prepend">
                                    <div className="input-group-text">
                                        <i className="bi-eye-fill" onClick={e => viewPassword(e)} />
                                    </div>
                                </span>
                                <input className="did-floating-input" type={inputType ? "password" : "text"} placeholder="" name="password" required />
                                <label className="did-floating-label">Password</label>
                            </div>
                            <button className="w-100 mb-2 btn rounded-4 btn-primary" type='submit' disabled={authLoader}>
                                {authLoader ? <div className="spinner-border text-primary" /> : "Sign in"}</button>
                            <small className="text-muted">By clicking Sign in, you agree to the terms of use.</small>
                            <div className=''>
                                <hr className="my-4" />
                                <h2 className="fs-5 fw-bold mb-3">Or use a third-party</h2>
                                <GoogleSocialLogin />

                            </div>
                        </form>
                    </div>
                    <div className="modal-footer border-top-0">
                        <ul className="nav justify-content-between w-100">
                            <li className="nav-item">
                                <Link to="/" className="nav-link fs-7 text-black-50 fw-bold">Innovest shop</Link>
                            </li>
                            <ul className="nav justify-content-end">
                                <li className="nav-item">
                                    <Link to="/auth/signup" className="nav-link fs-7 text-black-50">Sign up</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link fs-7 text-black-50" to="/legal/privacy">Privacy</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link fs-7 text-black-50" to="/legal/terms">Terms</Link>
                                </li>
                            </ul>
                        </ul>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Login;