import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../Api';
import component from '../../Components';
import redux from '../../ReduxMapper';
import Utils from '../../utils/utils';
import CartItemRow from './CartItemRow';

class CartView extends Component {

    state = {
        loading: false,
        error: null,
        success: null
    }

    addCoupon = event => {
        event.preventDefault()
        this.setState({ loading: true, error: null, success: null })
        APICall(Api.addCoupon(event.target.code.value), response => {
            event.target.reset();
            this.props.localCartUpdated(response.data.order);
            this.setState({ loading: false, error: null, success: response.data.details },
                () => {
                    setTimeout(() => {
                        this.setState({ success: null })
                    }, 3000);
                })
        }, response => {
            if (response.data)
                this.setState({ error: response.data.details, loading: false });
            if (response.error)
                this.setState({ loading: false, error: response.error })
        }, "adding coupon")
    }
    render() {
        const { loading, error, success } = this.state;
        const { cartProducts: { cartItems, cartLoader }, localCartUpdated } = this.props;
        let is_cart;
        if (cartItems !== null) {
            is_cart = cartItems !== null && Object.entries(cartItems).length !== 0;
        }
        if (cartItems === "undefined") {
            is_cart = {}
        }
        return (
            <React.Fragment>
                <component.BreadCrumb
                    title={"Your cart"}
                    current={"Cart"}
                    links={[{ name: 'Home', to: '/' }, { name: 'Shop', to: '/shop/products/' }]}
                />
                <div className="container pb-5 mb-2 mb-md-4">
                    {!is_cart && !cartLoader &&
                        <div className="col-lg-6 col-md-8 mx-auto my-5 py-5">
                            <h1 className="fw-light">Your cart is empty.</h1>
                            <p className="lead text-muted">Buy more items to increase your chances of being awarded free <span className="text-success fw-bold"> shopping credit. </span> Check more items in the trending section, most of them have discounts. </p>
                            <p>
                                <Link to="/shop/products" className="btn btn-primary my-2">Continue shopping</Link>
                            </p>
                        </div>
                    }
                    {cartLoader &&
                        <div className="row mt-3">
                            <div className="text-center my-5 py-5">
                                <div className="spinner-border text-primary" />
                            </div>
                        </div>
                    }
                    {cartItems && cartItems.items.length > 0 &&
                        <>
                            <div className="row mt-3">
                                <div className="col-lg-8 ">
                                    {
                                        is_cart && cartItems.items.map(item => {
                                            const product = Utils.objectUpdate(item, {
                                                localCartUpdated,
                                            })
                                            return (
                                                <CartItemRow {...product} key={item.id} />
                                            )
                                        })
                                    }
                                </div>
                                <aside className="col-lg-4 pt-4 pt-lg-0 ps-xl-5">
                                    <div className="bg-white rounded-3 shadow-lg p-4">
                                        <div className="py-2 px-xl-2">
                                            <div className="text-center mb-4 pb-3 ">
                                                <h2 className="h4 mb- pb-1">Subtotal</h2>
                                                <h2 className="fw-normal mb-5">${(cartItems.order_total).toFixed(2)}</h2>
                                                <h2 className="h6 pb-1 text-muted">Total discount</h2>
                                                <h5 className="fw-normal text-muted">${(cartItems.total_discount).toFixed(2)}</h5>
                                                <div>
                                                    {cartItems.coupon && cartItems.coupon.amount &&
                                                        <li className="list-group-item d-flex justify-content-between bg-light">
                                                            <div className="text-success">
                                                                <h6 className="my-0">Promo code
                                                                    {cartItems.coupon.redeemed &&
                                                                        <i className="bi bi-check-circle-fill ms-2 text-success" />}
                                                                </h6>
                                                                <small>{cartItems.coupon.code}</small>
                                                            </div>
                                                            <span className="text-success">−${cartItems.coupon.amount}</span>
                                                        </li>
                                                    }
                                                </div>
                                                <div>
                                                    {error && <div className="alert alert-danger my-2" role="alert">{JSON.stringify(error)}</div>}
                                                    {success && <div className="alert alert-success my-2" role="alert">{JSON.stringify(success)}</div>}
                                                    <form className='mt-3' onSubmit={e => this.addCoupon(e)}>
                                                        <div className="did-floating-label-content">
                                                            <input className=" did-floating-input" type="text" placeholder="" name='code' />
                                                            <label className="did-floating-label">Promo code</label>
                                                        </div>
                                                        <div>
                                                            <button disabled={loading} className="btn btn-outline-warning btn-sm px-lg-4">
                                                                {loading ? <div className='spinner-border text-warning' /> : "Apply promo code"}
                                                            </button>
                                                        </div>
                                                    </form>
                                                    <Link to="/cart/shipping-address" className="btn btn-warning btn-shadow d-block w-100 mt-4">
                                                        <i className="bi bi-card fs-lg me-2" />
                                                        Proceed to Checkout
                                                    </Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </aside>
                            </div>
                        </>
                    }
                </div>
            </React.Fragment>

        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(CartView);
