
import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../Api';

import Main from '../../Main';
import redux from '../../ReduxMapper';


class Navbar extends Component {
    componentDidMount() {
        this.fetchUserProfile();
    }
    fetchUserProfile = () => {
        this.setState({ loading: true });
        APICall(Api.getBuyerProfile(), success => {
            if (success.data.buyer_profile) {
                this.props.localProfileBuyerUpdated(success.data.buyer_profile);
                this.setState({ loading: false, error: null })
            };
        }, error => {
            console.log(error);
            this.setState({ loading: false, error })
        }, 'retrieving buyer profile')
    }
    render() {
        const { profileData: { profileBuyerProfile } } = this.props;
        return (
            <div>
                <header id="profile_header" className="profile_header fixed-top d-flex align-items-center">

                    <div className="d-flex align-items-center justify-content-between">
                        <a href="/" className="profile_logo d-flex align-items-center">
                            <img src="assets/img/logo.png" alt="" />
                            <span className="d-none d-lg-block">Innovest shop</span>
                        </a>
                        <i className="bi bi-list toggle-sidebar-btn" onClick={e => Main.toggleSidebar(e)} />
                    </div>
                    <div className="search-bar d-none">
                        <form className="search-form d-flex align-items-center" method="POST" action="#">
                            <input type="text" name="query" placeholder="Search" title="Enter search keyword" />
                            <button type="submit" title="Search"><i className="bi bi-search"></i></button>
                        </form>
                    </div>
                    <nav className="header-nav ms-auto">
                        <ul className="d-flex align-items-center">
                            <li className="nav-item d-none">
                                <a className="nav-link nav-icon search-bar-toggle " href="/" onClick={e => Main.searchBarToggle(e)}>
                                    <i className="bi bi-search"></i>
                                </a>
                            </li>

                            <li className="nav-item  d-none position-relative">
                                <span className="nav-link nav-icon dropdown" onClick={e => Main.toggleFilter(e)}>
                                    <i className="bi bi-chat-left-text"></i>
                                    <span className="badge bg-success badge-number">3</span>
                                </span>
                                <ul className="dropdown-menu dropdown-menu-end dropdown-menu-arrow messages">
                                    <li className="dropdown-header">
                                        You have 3 new messages
                                        <a href="/">
                                            <span className="badge rounded-pill bg-primary p-2 ms-2">View all</span>
                                        </a>
                                    </li>
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li className="message-item">
                                        <a href="/">
                                            <img src="assets/img/messages-1.jpg" alt="" className="rounded-circle" />
                                            <div>
                                                <h4>Maria Hudson</h4>
                                                <p>Velit asperiores et ducimus soluta repudiandae labore officia est ut...</p>
                                                <p>4 hrs. ago</p>
                                            </div>
                                        </a>
                                    </li>
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>

                                    <li className="dropdown-footer">
                                        <a href="/">Show all messages</a>
                                    </li>
                                </ul>
                            </li>

                            <li className="nav-item  pe-3">
                                {profileBuyerProfile &&
                                    <span className="nav-link nav-profile d-flex align-items-center pe-0 dropdown cursor-p" onClick={e => Main.toggleFilter(e)}>
                                        {profileBuyerProfile.avatar && <img src={profileBuyerProfile.avatar} alt="Profile" className="rounded-circle" />}
                                        {profileBuyerProfile.account.first_name && <span className="d-none d-md-block dropdown-toggle ps-2">{profileBuyerProfile.account.first_name} {profileBuyerProfile.account.last_name}</span>}
                                        {!profileBuyerProfile.account.first_name && <span className="d-none d-md-block dropdown-toggle ps-2">{profileBuyerProfile.account.email}</span>}
                                    </span>
                                }
                                <ul className="dropdown-menu dropdown-menu-end dropdown-menu-arrow profile">
                                    {profileBuyerProfile &&
                                        <li className="dropdown-header">
                                            {profileBuyerProfile.account.first_name && <h6>{profileBuyerProfile.account.first_name} {profileBuyerProfile.account.last_name}</h6>}
                                            {!profileBuyerProfile.account.first_name && <h6>{profileBuyerProfile.account.email}</h6>}
                                            <span>Role: {profileBuyerProfile.account.is_admin ? "Administrator" : "Buyer/Employee"}</span>
                                        </li>}
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li>
                                        <Link className="dropdown-item d-flex align-items-center" to="/profile/my-profile">
                                            <i className="bi bi-person"></i>
                                            <span>My Profile</span>
                                        </Link>
                                    </li>
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li>
                                        <Link className="dropdown-item d-flex align-items-center" to="/profile/settings">
                                            <i className="bi bi-gear"></i>
                                            <span>Account Settings</span>
                                        </Link>
                                    </li>
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li>
                                        <Link className="dropdown-item d-flex align-items-center" to="/profile/contact-us">
                                            <i className="bi bi-question-circle"></i>
                                            <span>Need Help?</span>
                                        </Link>
                                    </li>
                                    <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li>
                                        <span className="dropdown-item d-flex align-items-center cursor-p" data-bs-toggle="modal" data-bs-target="#logOutModal">
                                            <i className="bi bi-box-arrow-right"></i>
                                            <span>Sign Out</span>
                                        </span>
                                    </li>
                                </ul>
                            </li>

                        </ul>
                    </nav>
                </header>
                <aside id="sidebar" className="sidebar">
                    <ul className="sidebar-nav" id="sidebar-nav">
                        <li className="nav-heading pb-3 border-bottom">Dashboard</li>
                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/profile/dashboard/">
                                    <i className="bi bi-grid"></i>
                                    <span>My Dashboard</span>
                                </Link>
                            </span>
                        </li>

                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/profile/my-profile">
                                    <i className="bi bi-person"></i>
                                    <span>My profile</span>
                                </Link>
                            </span>
                        </li>

                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/profile/my-addresses">
                                    <i className="bi bi-house-door"></i>
                                    <span>My Addresses</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/profile/settings">
                                    <i className="bi bi-gear"></i>
                                    <span>Account settings</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/legal/faqs">
                                    <i className="bi bi-question-circle"></i>
                                    <span>FAQs</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/profile/contact-us">
                                    <i className="bi bi-envelope"></i>
                                    <span>Contact us</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-heading pb-2 border-bottom">Legal</li>
                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/legal/terms">
                                    <i className="bi bi-envelope"></i>
                                    <span>Terms</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-item">
                            <span className="nav-link collapsed">
                                <Link className="text-dark" to="/legal/privacy">
                                    <i className="bi bi-envelope"></i>
                                    <span>Privacy</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-heading border-bottom"></li>
                        <li className="nav-item" onClick={e => e.preventDefault()}>
                            <span className="nav-link bg-primary cursor-p ">
                                <Link className='text-white' to="/auth/signup">
                                    <i className="bi bi-person-plus text-white"></i>
                                    <span>Add a person</span>
                                </Link>
                            </span>
                        </li>
                        <li className="nav-item">
                            <span className="nav-link bg-warning bg-opacity-50 cursor-p" data-bs-toggle="modal" data-bs-target="#logOutModal">
                                <span className="text-dark">
                                    <i className="bi bi-power text-dark"></i>
                                    <span>Sign out</span>
                                </span>
                            </span>
                        </li>

                        <li className="nav-item d-none">
                            <span className="nav-link collapsed" onClick={e => Main.toggleDropDown(e)}>
                                <i className="bi bi-journal-text" />
                                <span>Forms</span>
                                <i className="bi bi-arrow-down-circle ms-auto fs-5"></i>
                            </span>
                            <ul id="forms-nav" className="nav-content">
                                <li>
                                    <a href="forms-elements.html">
                                        <i className="bi bi-circle"></i><span>Form Elements</span>
                                    </a>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </aside>
            </div>
        )
    }
}



export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Navbar)