import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Outlet, useLocation, useNavigate } from 'react-router-dom'
import component from '../Components';
import DevInfo from '../DevInfo';
import redux from '../ReduxMapper';
import Utils from '../utils/utils';

class CartComponent extends Component {
    state = {}
    componentDidMount() {
        this.props.getCart()
    }
    static getDerivedStateFromProps(props, state) {
        try {
            const isNull = props.cartProducts.cartItems === null
            if (!isNull) {
                if (Object.entries(props.cartProducts.cartItems).length === 0)
                    props.navigate("/shop/products")
            }
        } catch (error) { console.log(error) }
        return null;
    }
    render() {
        return (
            <React.Fragment>
                <DevInfo {...doc} />
                <component.Nav1 {...this.props} />
                <Outlet />
            </React.Fragment>
        )
    }
}
const Cart = (props) => {
    const navigate = useNavigate()
    const location = useLocation()
    const properties = Utils.objectUpdate(props, {
        navigate, location
    })
    return <CartComponent {...properties} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Cart);

