import React, { useEffect, useState } from 'react'
import Api from '../Api';
import Utils from '../utils/utils';


/**
 * @summary returns a hidden input with a value of csrf token after 
 * calling getCsrfToken which retrieves new token
 * @returns 
 */
export const CsrfToken = () => {
    const [csrftoken, setCsrftoken] = useState("");
    useEffect(() => {
        Api.getCSRFToken()
            .then(response => {
                setCsrftoken(response.data.csrf_token);
            })
            .catch(error => {
                console.log(error);
                return null
            })
    }, [])
    return (
        <input className="form-control" type="hidden" name="csrfmiddlewaretoken" value={csrftoken} disabled />
    )
}
export default CsrfToken;