import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../Api';
import redux from '../../ReduxMapper';
import { Address } from './addresses/Address';

class MyAddresses extends Component {
    state = {
        loading: false,
        error: null,
        success: null,
        userAddress: null,

        update_success: null,
        update_error: null,
        update_load: false
    }
    componentDidMount() {
        this.retriveAddress();
    }
    retriveAddress = () => {
        this.setState({ loading: true, error: null, success: null, userAddress: null })
        APICall(Api.retriveAddress(), success => {
            if (success.data.address) {
                this.setState({
                    loading: false, error: null, success: null, userAddress: success.data.address
                })
                return
            }
            this.setState({ loading: false, error: null, success: success.data.details, userAddress: success.data.address })
        }, error => {
            if (error.data)
                this.setState({ error: error.data.details, loading: false });
            if (error.error)
                this.setState({ loading: false, error: error.error })
        }, "retrieving your address")

    }

    /**
     * Create and Update can use same form but for now in A Lazy way
     */
    crudAddress = (id, e) => {
        this.setState({ update_load: true })
        let object = { "id": id, }, send = false
        if (e.type === "change") {
            object["update"] = true
            object["_default"] = e.target.checked;
            object["to_update"] = true
            send = true
        } if (e.type === "click") {
            object["delete"] = true
            send = true
        } if (e.type === "submit") {
            const formData = new FormData(e.target);
            if (e.target.id === "update_form") {
                formData.append('id', id);
                formData.append('update', true);
            } if (e.target.id === "create_form") {
                formData.append('create', true);
            }
            object = formData
            send = true
        }
        if (send)
            return APICall(Api.crudAddress(object), success => {
                const data = success.data;
                this.setState({ update_load: false })
                console.log(data)
                if (data.default) {
                    this.setState({ update_success: data.update_success, userAddress: data.address })
                }
                if (data.delete) {
                    this.setState({ update_success: data.update_success, userAddress: data.address })
                } if (data.create) {
                    this.setState({ update_success: data.update_success, userAddress: data.address })
                }
            }, error => {
                console.log(error);
            }, "retrieving your address")

    }
    render() {
        const { userAddress, update_success, update_error, update_load, loading } = this.state;

        return (
            <React.Fragment>
                <div className="dashboard__titile">
                    <h1>Shipping addresses</h1>
                    <nav>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <Link to="/">Home</Link>
                            </li>
                            <li className="breadcrumb-item active">Addresses</li>
                        </ol>
                    </nav>
                </div>
                <div className="contact">
                    <div className="col-lg-12 my-3">
                        <div className="accordion" id="accordionExample">
                            <div className="accordion-item border-2 border-primary">
                                <h2 className="accordion-header" id="headingOne">
                                    <button className="accordion-button btn-sm text-dark d-inline bg-primary bg-opacity-50" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne">
                                        <i className="bi bi-envelope-plus-fill me-2 fs-5"></i> Add shipping address
                                    </button>
                                </h2>
                                <div id="collapseOne" className="accordion-collapse collapse">
                                    <div className="accordion-body">
                                        <div className="d-flex justify-content-center">
                                            <div className="col-lg-8">
                                                <form className='billingForm' onSubmit={event => {
                                                    event.preventDefault();
                                                    if (!this.state.update_load) {
                                                        this.crudAddress(null, event)
                                                    }
                                                }} id="create_form">
                                                    <div>
                                                        <div className="row g-3">
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='first_name' placeholder="" required />
                                                                    <label className="did-floating-label">First name</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='last_name' placeholder="" required />
                                                                    <label className="did-floating-label">Second name</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className="did-floating-input" type="text" name='phone' placeholder="" required />
                                                                    <label className="did-floating-label">Phone number (+123 ... )</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='address' placeholder="" required />
                                                                    <label className="did-floating-label">Address /Town</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <input className=" did-floating-input" type="text" name='state' placeholder="" required />
                                                                    <label className="did-floating-label">ZIP code /County</label>
                                                                </div>
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <div className="did-floating-label-content">
                                                                    <select name="country" className="did-floating-select ">
                                                                        <option value="KE" >Kenya</option>
                                                                    </select>
                                                                    <label className="did-floating-label">Country</label>
                                                                </div>

                                                            </div>
                                                        </div>


                                                        <div className="ps-2 d-flex flex-column">
                                                            {update_load && <div className="spinner-border text-primary" />}
                                                            {update_error &&
                                                                <div className="alert alert-danger alert-dismissible fade show  w-100" role="alert">
                                                                    <i className="bi bi-exclamation-diamond-fill fs-3 me-3"></i> <strong>Error: </strong> {update_error}
                                                                    <button type="button" className="btn-close" onClick={e => this.setState({ update_error: null })}></button>
                                                                </div>
                                                            }
                                                            {update_success &&
                                                                <div className="alert alert-success alert-dismissible fade show w-100" role="alert">
                                                                    <i className="bi bi-check-circle-fill fs-3 me-3"></i> <strong>Success: </strong> {update_success}
                                                                    <button type="button" className="btn-close" onClick={e => this.setState({ update_success: null })}></button>
                                                                </div>
                                                            }
                                                            {!update_load &&
                                                                <button type='submit' className="btn btn-success w-25">
                                                                    <span className="d-none d-sm-inline">Add shipping address</span>
                                                                </button>
                                                            }

                                                        </div>
                                                    </div>

                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        {userAddress && userAddress.length < 1 && !loading &&
                            <React.Fragment>
                                <div className="col-lg-12">
                                    <div className="alert alert-info bg-info alert-dismissible fade show">
                                        <div className="card text-dark bg-info mb-3 border-0" >
                                            <div className="card-body p-0">
                                                <h5 className="card-title">No shipping ddresses</h5>
                                                <p className="card-text">Your addresses will appear here.</p>
                                            </div>
                                        </div>
                                        <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                    </div>

                                </div>
                            </React.Fragment>
                        }

                        {userAddress && userAddress.map((addr, i) => {
                            return (
                                <React.Fragment key={i}>
                                    <Address {...addr} crudMethod={this.crudAddress} {...this.state} />
                                </React.Fragment>
                            )
                        })}

                    </div>
                </div>
            </React.Fragment>

        )
    }
} export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(MyAddresses)