const Main = {
     select: (el, all = false) => {
          el = el.trim()
          if (all) {
               return [...document.querySelectorAll(el)]
          } else {
               return document.querySelector(el)
          }
     },

     event: (type, el, listener, all = false) => {
          let selectEl = Main.select(el, all)
          if (selectEl) {
               if (all) {
                    selectEl.forEach(e => e.addEventListener(type, listener))
               } else {
                    selectEl.addEventListener(type, listener)
               }
          }
     },

     headerFixed: () => {
          let selectHeader = document.querySelector('#header');
          if (selectHeader) {
               document.addEventListener('scroll', () => {
                    window.scrollY > 50 ?
                         selectHeader.classList.add('sticked') :
                         selectHeader.classList.remove('sticked');
               });
          }
     },

     mobileNavToogle: () => {
          const mobileNavShow = document.querySelector('.mobile-nav-show');
          const mobileNavHide = document.querySelector('.mobile-nav-hide');
          const togglers = document.querySelectorAll('.mobile-nav-toggle');
          const toggleMobile = () => {
               document.querySelector('body').classList.toggle('mobile-nav-active');
               mobileNavShow.classList.toggle('d-none');
               mobileNavHide.classList.toggle('d-none');
          }
          togglers.forEach(toggler => {
               toggler.addEventListener("click", e => {
                    e.preventDefault();
                    toggleMobile();
               })
          });
     },

     togglescrollTop: () => {
          const scrollTop = document.querySelector('.scroll-top');
          if (scrollTop) {
               window.scrollY > 100 ? scrollTop.classList.add('active') : scrollTop.classList.remove('active');
          } else {
               return
          }
     },

     dropdownListener: (event) => {
          if (!event.target.matches('.dropdown')) {
               var dropdowns = document.getElementsByClassName("dropdown-menu");
               var i;
               for (i = 0; i < dropdowns.length; i++) {
                    var openDropdown = dropdowns[i];
                    if (openDropdown.classList.contains('show')) {
                         openDropdown.classList.remove('show');
                    }
               }
          }
     },
     toggleFilter: e => {
          e.stopPropagation();
          const element = e.currentTarget;
          element.nextSibling.classList.toggle("show")
     },
     toggleOrders: e => {
          e.preventDefault();
          let id = e.currentTarget.id
          let table = document.querySelector(`[data-table="${id}"]`)
          if (table.classList.contains("show")) {
               table.classList.add("fade_in")
               setTimeout(() => {
                    table.classList.remove("show")
                    table.classList.remove("fade_in")
               }, 300);
          } else {
               table.classList.add("show")
          }
     },
     toggleDropDown: e => {
          e.preventDefault();
          const thisElement = e.currentTarget;
          thisElement.classList.toggle("collapsed");
          thisElement.nextSibling.classList.toggle("show")
     },
     toggleSidebar: e => {
          if (e.currentTarget) {
               document.querySelector('body').classList.toggle("toggle-sidebar")
          }
     },
     searchBarToggle: e => {
          e.preventDefault();
          if (e.currentTarget) {
               document.querySelector('.search-bar').classList.toggle("search-bar-show")
          }
     }

}

export default Main;