import React, { Component } from 'react'
import redux from '../ReduxMapper'
import { connect } from 'react-redux';
import { Outlet } from "react-router-dom";
import DevInfo from '../DevInfo'
class Auth extends Component {
    componentDidMount() {
        let body = document.querySelector('.mobile-nav-active');
        if (body) body.classList.remove("overflow-hidden")
    }
    render() {
        return (
            <section className='p-lg-5'>
                <DevInfo {...doc} />
                <Outlet />
            </section>
        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Auth);

const doc = {
    title: "Authentication Page",
    description: [
        ["This is the authentication page, Login and log out JS"],
        ["Few things to follow up"],
        ["-----------------------------------------------------------------------"],
        ["1. USER LOGIN: (User authentication)"],
        ["      When user logs in, will be directed to home page"],
        ["      Relevant links to authenticated user will be visible"],
        ["      Once the user is authenticated, will be able to add items in cart and create order"],
        ["   TODO:"],
        ["      - Email verification"],
        ["      - Password reset"],
        ["      - Account deletion"],
        ["      - Social authentication, Google, twitter, Facebook, Tiktok and Instagram"],
        ["-----------------------------------------------------------------------"],
        ["2. USER REGISTRATION: (User creation)"],
        ["      User can create an account and buyer flag will be set True"],
        ["      Once account is created, user will be authenticated directly and redirected to home page"],
        ["   TODO:"],
        ["      - Email verification"],
    ]
}