import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Api, { APICall } from '../../Api';
import CsrfToken from '../../redux/CsrfToken';
import redux from '../../ReduxMapper';




class MyProfile extends Component {
    constructor(props) {
        super(props)
        this.avatar_upload = React.createRef();
        this.state = {
            loading: false,
            profileUploadLoader: false,
            passwordLoader: false,
            error: null,
            errorPassword: null,
            successProfile: null,
            successPassword: null,
            viewPassword: false,
            data: null,
            avatar: null,
        }
    }
    componentDidMount() {
        this.fetchUserProfile();
    }
    tabEventListener = e => {
        // localStorage.removeItem("profile_page");
        let profile_page = e.currentTarget.getAttribute('data-bs-target');
        localStorage.setItem("profile_page", profile_page);
    }

    setActiveTab = e => {
        const tabs = document.querySelectorAll('[data-bs-toggle="tab"]');
        const panes = document.querySelectorAll(".tab-pane")
        const activeTab = localStorage.getItem("profile_page");
        if (activeTab) {
            panes.forEach(pane => {
                pane.classList.remove("show");
                pane.classList.remove("active");
            })
            let active_pane = document.querySelector(`${activeTab}`);
            let active_tab = document.querySelector(`[data-bs-target="${activeTab}"]`);
            if (active_pane) {
                active_pane.classList.add("show");
                active_pane.classList.add("active");
            }

            tabs.forEach(element => {
                element.classList.remove('active')
            });
            if (active_tab)
                active_tab.classList.add("active")
        }
    }
    handlePasswordChange = e => {
        e.preventDefault();
        this.setState({ passwordLoader: true })
        if (e.target.newpassword.value !== e.target.renewpassword.value) {
            this.setState({ passwordLoader: false, errorPassword: "New passwords do not match." });
            return;
        }
        const formData = new FormData(e.target);
        APICall(Api.changePassword(formData), success => {
            const data = success.data
            if (data.passwordError) {
                this.setState({ passwordLoader: false, errorPassword: data.passwordError })
            }
            if (data.passwordSuccess) {
                this.setState({ passwordLoader: false, successPassword: data.passwordSuccess, errorPassword: null })
            }
        }, error => {
            this.setState({ passwordLoader: false, errorPassword: JSON.stringify(error) })
        })
    }
    handleProfileUpdate = e => {
        e.preventDefault();
        this.setState({ profileUploadLoader: true })
        const formData = new FormData();
        if (e.target.avatar.files[0])
            formData.append('avatar', e.target.avatar.files[0])
        if (e.target.address.value) {
            formData.append('address', e.target.address.value)
        } if (e.target.phone.value) {
            formData.append('phone', e.target.phone.value)
        } if (e.target.fullNames.value) {
            formData.append('full_name', e.target.fullNames.value)
        } if (e.target.gender.value) {
            formData.append('gender', e.target.gender.value)
        }
        this.setState({ profileUploadLoader: false })
        APICall(Api.createBuyerProfile(formData), success => {
            if (success.data.updated) {
                this.fetchUserProfile()
                this.setState({ successProfile: success.data.details })
            }
            if (!success.data.updated) {
                this.setState({ error: success.data.error })
            }
        }, error => {
            console.log(error);

        }, 'updating your profile')
    }
    fetchUserProfile = () => {
        this.setState({ loading: true });
        APICall(Api.getBuyerProfile(), success => {
            if (success.data.buyer_profile) {
                this.props.localProfileBuyerUpdated(success.data.buyer_profile);
                this.setState({ loading: false, error: null })
            };
        }, error => {
            console.log(error);
            this.setState({ loading: false, error })
        }, 'retrieving buyer profile')
    }

    render() {
        const { loading, error, successProfile, profileUploadLoader, successPassword, viewPassword, errorPassword, passwordLoader, avatar } = this.state;
        const { profileData: { profileBuyerProfile } } = this.props;

        if (profileBuyerProfile) {
            this.setActiveTab();
        }
        return (
            <React.Fragment>
                <div className="dashboard__titile">
                    <h1>Profile</h1>
                    <nav>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item"><Link to="/">Home</Link></li>
                            <li className="breadcrumb-item active"></li>
                        </ol>
                    </nav>
                </div>

                <div className="section profile">
                    <div className="row">

                        {error &&
                            <div className="col-lg-12">
                                <div className="d-flex justify-content-center my-5">
                                    <div className="col-lg-8">
                                        <div className="dashboard__card dashboard__card-body p-0">
                                            <div className="d-flex justify-content-center align-content-center p-4">
                                                <div className="alert alert-danger alert-dismissible w-100 fade show m-0" role="alert">
                                                    <i className="bi bi-exclamation-triangle-fill me-2 fs-3" />
                                                    {
                                                        typeof error === 'object' ?
                                                            Object.keys(error).map((key, value) => {
                                                                return (
                                                                    <React.Fragment key={value}>
                                                                        <strong className='ms-1 text-capitalize'>{key}:</strong> <span>{error[key]}</span>
                                                                    </React.Fragment>
                                                                )
                                                            })
                                                            :
                                                            <span>{error}</span>
                                                    }
                                                    <button type="button" className="btn-close" onClick={e => this.setState({ error: null })} />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>}

                        <div className="col-xl-4">
                            <div className="dashboard__card">
                                {loading &&
                                    <div className="text-center my-3">
                                        <div className="spinner-border text-primary" />
                                    </div>
                                }
                                {!loading && profileBuyerProfile &&
                                    <div className="dashboard__card-body profile-card pt-4 d-flex flex-column align-items-center">
                                        {profileBuyerProfile.avatar && <img src={profileBuyerProfile.avatar} alt="Profile" className="rounded-circle" />}
                                        {profileBuyerProfile.account.first_name ?
                                            <h2> {profileBuyerProfile.account.first_name} {profileBuyerProfile.account.last_name}</h2>
                                            :
                                            <h2>{profileBuyerProfile.account.email}</h2>
                                        }

                                        <h3>{profileBuyerProfile.account.is_admin ? `Administrator` : 'Your profile'}</h3>
                                        <div className="social-links mt-2 d-none">
                                            <a href="/" className="twitter"><i className="bi bi-twitter"></i></a>
                                            <a href="/" className="facebook"><i className="bi bi-facebook"></i></a>
                                            <a href="/" className="instagram"><i className="bi bi-instagram"></i></a>
                                            <a href="/" className="linkedin"><i className="bi bi-linkedin"></i></a>
                                        </div>
                                    </div>
                                }
                            </div>

                        </div>

                        <div className="col-xl-8">

                            <div className="dashboard__card">
                                <div className="dashboard__card-body pt-3">
                                    {loading &&
                                        <div className="text-center my-3">
                                            <div className="spinner-border text-primary" />
                                        </div>
                                    }

                                    {!loading && profileBuyerProfile &&
                                        <React.Fragment>
                                            <ul className="nav nav-tabs nav-tabs-bordered">
                                                <li className="nav-item">
                                                    <button className="nav-link active" data-bs-toggle="tab" data-bs-target="#profile-overview" onClick={e => this.tabEventListener(e)}>Overview</button>
                                                </li>
                                                <li className="nav-item">
                                                    <button className="nav-link" data-bs-toggle="tab" data-bs-target="#profile-edit" onClick={e => this.tabEventListener(e)}>Edit Profile</button>
                                                </li>
                                                <li className="nav-item">
                                                    <button className="nav-link" data-bs-toggle="tab" data-bs-target="#profile-settings" onClick={e => this.tabEventListener(e)}>Settings</button>
                                                </li>
                                                <li className="nav-item">
                                                    <button className="nav-link" data-bs-toggle="tab" data-bs-target="#profile-change-password" onClick={e => this.tabEventListener(e)}>
                                                        Change Password</button>
                                                </li>
                                            </ul>
                                            <div className="tab-content pt-2">

                                                <div className="tab-pane fade  show active profile-overview" id="profile-overview">
                                                    <h5 className="dashboard__card-title">Profile Details</h5>
                                                    <div className="row">
                                                        <div className="col-lg-3 col-md-4 label">Email</div>
                                                        <div className="col-lg-9 col-md-8">{profileBuyerProfile.account.email}</div>
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-lg-3 col-md-4 label ">Full Name</div>
                                                        <div className="col-lg-9 col-md-8">
                                                            {profileBuyerProfile.account.first_name ?
                                                                <span> {profileBuyerProfile.account.first_name} {profileBuyerProfile.account.last_name}</span>
                                                                :
                                                                <span className="badge bg-warning text-dark align-text-bottom cursor-p" >Profile update</span>
                                                            }
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-lg-3 col-md-4 label">Address</div>
                                                        <div className="col-lg-9 col-md-8">{profileBuyerProfile.address ?
                                                            profileBuyerProfile.address
                                                            :
                                                            <span className="badge bg-warning text-dark align-text-bottom cursor-p" >Profile update</span>}</div>
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-lg-3 col-md-4 label">Phone</div>
                                                        <div className="col-lg-9 col-md-8">{profileBuyerProfile.phone ?
                                                            profileBuyerProfile.phone
                                                            :
                                                            <span className="badge bg-warning text-dark align-text-bottom cursor-p" >Profile update</span>}</div>
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-lg-3 col-md-4 label">Gender</div>
                                                        <div className="col-lg-9 col-md-8">
                                                            {
                                                                profileBuyerProfile.gender ?
                                                                    profileBuyerProfile.gender === "Select" ?
                                                                        <span className="badge bg-warning text-dark align-text-bottom cursor-p" >Profile update</span> : profileBuyerProfile.gender
                                                                    :
                                                                    <span className="badge bg-warning text-dark align-text-bottom cursor-p" >Profile update</span>}</div>
                                                    </div>

                                                </div>

                                                <div className="tab-pane fade profile-edit pt-3" id="profile-edit">
                                                    <form onSubmit={e => {
                                                        this.handleProfileUpdate(e)
                                                    }}>
                                                        <CsrfToken />
                                                        {successProfile &&
                                                            <div className="py-3">
                                                                <div className="alert alert-success alert-dismissible fade show" role="alert">
                                                                    <i className="bi bi-check-circle-fill me-2 fs-3" /><strong>Success: </strong>{successProfile}
                                                                    <button type="button" className="btn-close" onClick={e => this.setState({ successProfile: null })}></button>
                                                                </div>
                                                            </div>
                                                        }
                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Profile Image</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                {profileBuyerProfile.avatar && <img src={profileBuyerProfile.avatar} alt="Profile" />}
                                                                <button className="btn btn-primary btn-sm ms-4" type="button" title="Upload new profile image" onClick={e => this.avatar_upload.current.click()}>
                                                                    <i className="bi bi-upload"></i>
                                                                </button>
                                                                <div>
                                                                    <small className='fw-bold text-primary'>{avatar ? `${avatar.name}` : ''}</small>
                                                                </div>
                                                                <div className="pt-2 d-flex ">

                                                                    {profileBuyerProfile.avatar && <button className="btn btn-danger btn-sm ms-4 d-none" title="Remove my profile image">
                                                                        <i className="bi bi-trash"></i>
                                                                    </button>}
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <input type="file" name='avatar' className="d-none" ref={this.avatar_upload} onChange={e => this.setState({ avatar: e.target.files[0] })} />
                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Full Name</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <input name="fullNames" type="text" className="form-control" />
                                                            </div>
                                                        </div>

                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Address</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <input name="address" type="text" className="form-control" />
                                                            </div>
                                                        </div>

                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Phone <small className='fs-8'>(Formart +254)</small></label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <input name='phone' type="text" className="form-control" />
                                                            </div>
                                                        </div>

                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Gender </label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <select name='gender' className="form-select form-select-md" disabled={profileBuyerProfile.gender !== "Select"}>
                                                                    {profileBuyerProfile.gender === "Select" ?
                                                                        <React.Fragment>
                                                                            <option defaultValue value='s'>Select</option>
                                                                            <option value="m">Male</option>
                                                                            <option value="f">Female</option>
                                                                        </React.Fragment> :
                                                                        <option defaultValue>{profileBuyerProfile.gender}</option>
                                                                    }
                                                                </select>
                                                            </div>
                                                        </div>
                                                        <div className="text-end">
                                                            {profileUploadLoader && <div className="spinner-border text-primary" />}
                                                            {!profileUploadLoader && <button type="submit" className="btn btn-sm btn-primary">Save Changes</button>}
                                                        </div>
                                                    </form>

                                                </div>

                                                <div className="tab-pane fade pt-3" id="profile-settings">
                                                    <form>
                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Email Notifications</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <div className="form-check">
                                                                    <input className="form-check-input" type="checkbox" id="changesMade" disabled checked />
                                                                    <label className="form-check-label" >
                                                                        Changes made to your account
                                                                    </label>
                                                                </div>
                                                                <div className="form-check">
                                                                    <input className="form-check-input" type="checkbox" id="newProducts" disabled checked />
                                                                    <label className="form-check-label" >
                                                                        Information on new products and services
                                                                    </label>
                                                                </div>
                                                                <div className="form-check">
                                                                    <input className="form-check-input" type="checkbox" id="proOffers" disabled checked />
                                                                    <label className="form-check-label">
                                                                        Marketing and promo offers
                                                                    </label>
                                                                </div>
                                                                <div className="form-check">
                                                                    <input className="form-check-input" type="checkbox" id="securityNotify" disabled checked />
                                                                    <label className="form-check-label" >
                                                                        Security alerts
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>

                                                        <div className="text-center">
                                                            <button type="submit" className="btn btn-primary">Save Changes</button>
                                                        </div>
                                                    </form>

                                                </div>

                                                <div className="tab-pane fade pt-3" id="profile-change-password">
                                                    <form onSubmit={e => this.handlePasswordChange(e)}>
                                                        {successPassword &&
                                                            <div className="py-3">
                                                                <div className="alert alert-success alert-dismissible fade show" role="alert">
                                                                    <i className="bi bi-check-circle-fill me-2 fs-3" /><strong>Success: </strong>{successPassword}
                                                                    <button type="button" className="btn-close" onClick={e => this.setState({ successPassword: null })}></button>
                                                                </div>
                                                            </div>
                                                        }
                                                        {errorPassword &&
                                                            <div className="py-3">
                                                                <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                                                    <i className="bi bi bi-exclamation-circle-fill me-2 fs-3" /><strong>Error: </strong>{errorPassword}
                                                                    <button type="button" className="btn-close" onClick={e => this.setState({ errorPassword: null })}></button>
                                                                </div>
                                                            </div>
                                                        }

                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Current Password</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <input name="password" type={viewPassword ? "text" : "password"} className="form-control" required />
                                                            </div>
                                                        </div>

                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Password</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <input name="newpassword" type={viewPassword ? "text" : "password"} className="form-control" required />
                                                            </div>
                                                        </div>

                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label">Password again</label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <input name="renewpassword" type={viewPassword ? "text" : "password"} className="form-control" required />
                                                            </div>
                                                        </div>
                                                        <div className="row mb-3">
                                                            <label className="col-md-4 col-lg-3 col-form-label"></label>
                                                            <div className="col-md-8 col-lg-9">
                                                                <div className="form-check">
                                                                    <input className="form-check-input" type="checkbox" onChange={e => {
                                                                        this.setState({ viewPassword: !viewPassword })
                                                                    }} />
                                                                    <label className="form-check-label text-muted small">
                                                                        View password
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>

                                                        <div className="text-center">
                                                            {passwordLoader && <div className='spinner-border text-primary' />}
                                                            {<button type="submit" className="btn btn-primary">Change Password</button>}
                                                        </div>
                                                    </form>

                                                </div>

                                            </div>
                                        </React.Fragment>
                                    }
                                </div>
                            </div>

                        </div>
                    </div>
                </div>

            </React.Fragment>
        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(MyProfile);
