import React, { useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Route, Routes } from 'react-router-dom';
import AppHOC from "./AppHOC";
import component from "../Components";
import Main from "../Main";
import * as authActions from '../redux/auth/AuthLoginReducer';
import * as cartActions from '../redux/cart/CartActionsReducer';
import * as homeActions from '../redux/home/HomeProductsReducer';
const BaseApp = props => {
    const isAuthenticated = useSelector(state => state.authentication.isAuthenticated);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(authActions.checkAuthStatus())
        dispatch(homeActions.homeProducts())
    }, [dispatch])
    useEffect(() => {
        if (isAuthenticated)
            dispatch(cartActions.orderSummary())
    }, [dispatch, isAuthenticated])
    return (
        <React.Fragment>
            <Routes>
                <Route path="developer" element={
                    <section>
                        <div className="container">
                            <h1>Database</h1>
                        </div>
                        <div className="card" id="developer">
                            <div className="card-body" >

                            </div>

                        </div>
                    </section>
                } />
                <Route path="/" element={<component.Home {...props} />} />
                <Route path="/shop/*" element={<component.Shop />}>
                    <Route path="products" element={<component.Products />} />
                    <Route path='product/:productID' element={<component.Product />} />
                    <Route path="*" element={<component.NotFound />} />
                </Route>
                <Route path="*" element={<component.NotFound />} />
                <Route path="/auth/*" element={<component.Auth />} >
                    <Route path="signup" element={<component.Signup />} />
                    <Route path="login" element={<component.Login />} />
                </Route>
                {isAuthenticated &&
                    <React.Fragment>
                        <Route path="/profile/*" element={<component.Profile />} >
                            <Route path="dashboard" element={<component.Dashboard />} />
                            <Route path="settings" element={<component.Settings />} />
                            <Route path="my-profile" element={<component.MyProfile />} />
                            <Route path="my-addresses" element={<component.MyAddresses />} />
                            <Route path="contact-us" element={<component.ContactUs />} />
                            <Route path="*" element={<component.NotFound {...{ "link": "/profile/dashboard", "to": "Profile" }} />} />
                        </Route>
                        <Route path="/cart/*" element={<component.Cart />} >
                            <Route path='cart-view' element={<component.CartView />} />
                            <Route path='shipping-address' element={<component.ShippingAddress />} />
                            <Route path='payment' element={<component.Payment />} />
                            <Route path='order-review' element={<component.OrderReview />} />
                            <Route path="*" element={<component.NotFound />} />
                        </Route>
                        <Route path='complete-order' element={<component.CompleteOrder />} />
                    </React.Fragment>
                }

                <Route path="/legal/*" element={<component.Legal />} >
                    <Route path="terms" element={<component.Terms />} />
                    <Route path="faqs" element={<component.FAQs />} />
                    <Route path="contact" element={<component.Contact />} />
                    <Route path="privacy" element={<component.Privacy />} />
                    <Route path="*" element={<component.NotFound />} />
                </Route>

            </Routes>
            <component.ToTop />
        </React.Fragment>
    )
}


const finalApp = AppHOC(BaseApp, Main)


export default finalApp;

