import React, { Component } from 'react'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom';
import redux from '../../ReduxMapper';
import { connect } from 'react-redux';
import component from '../../Components';
import Api, { APICall } from '../../Api';

/**
*
* @mwangihub
* @summary Cart Component
* @class ProductChild
* @extends {Component}
* @param {object} props From redux => redux Mapper
* @return {JSX} return JSX <CartItemRow/>  
*
*/
class ProductsChild extends Component {
    constructor(props) {
        super(props)
        this.state = {
            cached_products: null,
            title: "All products",
        }
    }
    componentDidMount() {
        this.props.getHomeProducts();
    }
    static getDerivedStateFromProps(props, state) {
        if (!state.cached_products) {
            return {
                cached_products: props.products.homeProducts,
            }
        }
        return null;
    }
    setStateWithCartegory = e => {
        e.preventDefault();
        let cartegory = e.currentTarget.getAttribute('data-filter');
        if (cartegory === "all") {
            this.setState({ title: "All products", cached_products: this.props.products.homeProducts })
            return
        }
        const filteredProducts = this.props.products.homeProducts.filter(p => p.category === cartegory)
        this.setState({ title: cartegory, cached_products: filteredProducts })
    }
    render() {
        const { authentication: { isAuthenticated }, products: { homeProductsError, homeProductsLoader }, localCartUpdated, localHomeProductsUpdate } = this.props;
        const { cached_products, title } = this.state;
        return (
            <React.Fragment>
                <component.BreadCrumb
                    title={"Shopping grid"}
                    current={"Shop"}
                    links={[{ name: 'Home', to: '/' },]}
                />
                <section style={{ padding: "0px" }} className="product_list">
                    <div className="container pb-5 mb-2 mb-md-4">
                        <div className="row">

                            {/* Main Page */}
                            <div className='col-lg-9 mt-5 home_products'>
                                {cached_products && cached_products.length > 0 &&
                                    <div className="text-center">
                                        <div className="section-header mb-5">
                                            <h2 className='text-capitalize'>{title} ({cached_products.length})</h2>
                                        </div>
                                    </div>}
                                {
                                    homeProductsError &&
                                    <div className="d-flex justify-content-center mt-2">
                                        <div className="col-lg-6 col-md-8 col-sm-12">
                                            <div className="alert alert-danger">{homeProductsError}</div>
                                        </div>
                                    </div>
                                }
                                {homeProductsLoader && <div id="preloader" />}
                                {cached_products && cached_products.length > 0 ?
                                    <div className="row gy-4">
                                        {cached_products.map(product => {
                                            return (
                                                <div className="col-md-4 col-sm-6 px-2 mb-4" key={product.id}>
                                                    <div className="card product-card border-0">
                                                        {isAuthenticated &&
                                                            <React.Fragment>
                                                                {product.cart_count &&
                                                                    <button className="btn-action-view btn-sm" data-bs-toggle="tooltip" data-bs-placement="left" title={`${product.cart_count}- In your cart`}>
                                                                        <i className="bi bi-cart" />
                                                                    </button>
                                                                }
                                                            </React.Fragment>
                                                        }
                                                        <Link to={`/shop/product/${product.slug}/`} className="card-img-top overflow-hidden" >
                                                            <img src={`${product.image}`} alt="Product" />
                                                        </Link>
                                                        <div className="card-body py-2">
                                                            <span className="product-meta d-block fs-xs pb-1" >{product.category}</span>
                                                            <h3 className="product-title">
                                                                <Link to={`/shop/product/${product.slug}/`}>{product.title}</Link>
                                                            </h3>
                                                            <div className="d-flex justify-content-between">
                                                                <div className="product-price">
                                                                    {product.discounted_price > 0 && <span >${product.discounted_price}</span>}
                                                                    {product.discounted_price > 0 && <del className=" text-muted ms-2">${product.price}</del>}
                                                                    {product.discounted_price === 0 && <span >${product.price} {product.stock}</span>}
                                                                </div>
                                                                <div className="star-rating">
                                                                    {/* to implement */}
                                                                    <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                    <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                    <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                    <i className="star-rating-icon bi bi-star-fill active"></i>
                                                                    <i className="star-rating-icon bi bi-star-fill"></i>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="card-body card-body-hidden">
                                                            {isAuthenticated &&
                                                                <div className="d-flex mb-2 flex-column">
                                                                    {
                                                                        product.stock > 0 ?
                                                                            <button className="btn btn-outline-primary btn-sm" onClick={
                                                                                e => APICall(Api.addToCart(product.slug), response => {
                                                                                    if (response.data.item) {
                                                                                        this.setState({ cached_products: response.data.items })
                                                                                        localCartUpdated(response.data.order);
                                                                                        localHomeProductsUpdate(response.data.items);
                                                                                    }
                                                                                })
                                                                            }>
                                                                                <i className="bi bi-cart-plus-fill" /> Add to Cart
                                                                            </button>
                                                                            :
                                                                            <button className="btn btn-sm btn-secondary" disabled>Out of stcok</button>
                                                                    }
                                                                </div>

                                                            }
                                                            <div className="text-center">
                                                                <Link to={`/shop/product/${product.slug}/`} className='btn btn-sm btn-light'>
                                                                    <i className="bi bi-eye me-1" />View
                                                                </Link>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <hr className="d-sm-none" />
                                                </div>
                                            )
                                        })
                                        }
                                    </div>
                                    :
                                    <h1>No products</h1>
                                }

                            </div>

                            {/* Side page */}
                            <div className="col-lg-3 d-none d-lg-block mt-5" data-aos="fade-up" data-aos-delay="400" >
                                <div className="sidebar_col ps-lg-4">
                                    <div className="sidebar-item search-form d-none">
                                        <h5 className="sidebar-title">Search</h5>
                                        <form action="" className="mt-3">
                                            <input type="text" />
                                            <button type="submit">
                                                <i className="bi bi-search"></i>
                                            </button>
                                        </form>
                                    </div>
                                    <div className="sidebar-item categories">
                                        <h5 className="sidebar-title">Categories</h5>
                                        <ul className="mt-3">
                                            <li><a href="/" data-filter="all" onClick={e => this.setStateWithCartegory(e)}>All</a></li>
                                            <li><a href="/" data-filter="men's clothing" onClick={e => this.setStateWithCartegory(e)}>Men's clothing</a></li>
                                            <li><a href="/" data-filter="women's clothing" onClick={e => this.setStateWithCartegory(e)}>Women's clothing </a></li>
                                            <li><a href="/" data-filter="electronics" onClick={e => this.setStateWithCartegory(e)}>Electronics </a></li>
                                            <li><a href="/" data-filter="jewelery" onClick={e => this.setStateWithCartegory(e)}>Jewelery </a></li>
                                        </ul>
                                    </div>
                                    <div className="sidebar-item tags d-none">
                                        <h5 className="sidebar-title">Tags</h5>
                                        <ul className="mt-3">
                                            <li><a href="/">App</a></li>
                                            <li><a href="/">IT</a></li>
                                            <li><a href="/">Business</a></li>
                                            <li><a href="/">Mac</a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </section >
            </React.Fragment >
        )
    }
}

const Products = (props) => {
    const navigate = useNavigate()
    const { productID } = useParams()
    const location = useLocation()
    const properties = {
        navigate, productID, location
    }

    return <ProductsChild {...properties} {...props} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Products);
