import axios from "axios";
import Utils from "./utils/utils";


let http = (token = null, c_type = "application/json; charset=UTF-8") => {

    let csrftoken_header = Utils.getCookie('csrftoken');
    // if (token) csrftoken_header = token;
    let header = {
        "Content-type": c_type,
        'Accept': c_type,
        'X-CSRFToken': csrftoken_header,
        // credentials: "same-origin",
    }
    // console.log(header);
    return axios.create({
        baseURL: localStorage.getItem("host"),
        headers: header
    });

};

const requestSocialAppKey = obj => {
    return http().post("api/user/social/key", obj);
}
const authWithGoogle = obj => {
    return http().post("api/user/google/", obj);
}
const authSignUp = (obj) => {
    const { token } = obj;
    return http(token).post("api/user/v1/signup/", obj);
};
const login = (email, password) => {
    return http().post("api/user/v1/login/", { email, password });
};
const logOut = token => {
    return http(token).post("api/user/v1/logout/");
};
const checkAuth = () => {
    return http().get("api/user/v1/checkauth/");
};
const getCSRFToken = () => {
    return http().get("api/user/get-csrf-token/");
};
const getBuyerProfile = () => {
    return http().get("api/user/v1/buyer-profile/");
};
const createBuyerProfile = (object) => {
    return http().post("api/user/v1/buyer-profile/", object);
};
const changePassword = (object) => {
    return http().post("api/user/v1/change-password/auth-user/", object);
};
const sendMessage = (object) => {
    return http().post("api/user/v1/web-messages/", object);
};



// Shop Apis
const itemDetail = (id) => {
    return http().get(`api/shop/items-detail/${id}/`);
};
const homeProducts = () => {
    return http().get('api/shop/items-list/');
};
const orderSummary = () => {
    return http().get('api/shop/order-summary/');
};
const deleteFromCart = (slug) => {
    return http().post('api/shop/delete-from-cart/', { slug });
};
const reduceFromCart = (slug) => {
    return http().post('api/shop/reduce-from-cart/', { slug });
};
const addItemsToCart = (slug) => {
    return http().post('api/shop/add-items-to-cart/', { slug });
};
const addToCart = (slug) => {
    return http().post('api/shop/add-to-cart/', { slug });
};
const addCoupon = (code) => {
    return http().post('api/shop/add-coupon/', { code })
};
const createAddress = (data) => {
    return http().post('api/shop/create-addresses/', data)
};
const retriveAddress = () => {
    return http().post(`api/shop/retrieve-address/`,)
};
const mpesaPay = data => {
    return http().post(`api/shop/mpesa-pay/`, data)
};
const completedOrder = () => {
    return http().get(`api/shop/completed-order/`,)
};
const autoCreateItems = () => {
    return http().post(`api/shop/auto-items/`,)
};
const crudAddress = (object) => {
    return http().post("api/shop/crud-shippingAddr/", object);
};

const Api = {
    authSignUp,
    login,
    logOut,
    checkAuth,
    getCSRFToken,
    getBuyerProfile,
    createBuyerProfile,
    changePassword,
    sendMessage,
    requestSocialAppKey,
    homeProducts,
    authWithGoogle,

    orderSummary,
    deleteFromCart,
    addItemsToCart,
    reduceFromCart,
    addToCart,
    addCoupon,
    createAddress,
    retriveAddress,
    itemDetail,
    mpesaPay,
    completedOrder,
    autoCreateItems,
    crudAddress,
};

/**
 * @mwangihub
 * @summary Reusable Api call function
 * @Function APICall
 * @param {object} api function from API module with its parameters. Expected to return a promise 
 * @param {object} success custom function to be implemented when the promise is fullfilled
 * @param {object} error custom function to be implemented when the promise is Rejected
 * @param {String} description A simple string thta describes the intention of the Api. e.g  "fetching list of users"
 * @return {object} promise
 */
export const APICall = (api, success = null, error = null, description = null) => {
    api.then(response => {
        // console.log(response)
        if (success)
            success(response)
    }).catch(errors => {
        // console.log(errors);
        if (errors.response) {
            if (error)
                error(errors.response)
        } else if (errors.request) {
            console.log(errors);
            if (error)
                error({ "error": "There was a network error" })
        } else {
            console.log(errors);
            if (error)
                if (description)
                    error({ "error": `Another error occured while ${description}` });
                else
                    error({ "error": "Another error occured" })
        }
    })
}
export default Api