import React, { Component } from 'react'
import redux from '../ReduxMapper'
import { connect } from 'react-redux';
import { Outlet, useLocation, useNavigate } from "react-router-dom";

import component from '../Components';
import DevInfo from '../DevInfo';
class ShopRender extends Component {

    render() {
        return (
            <React.Fragment>
                <DevInfo {...doc} />
                <component.Nav1 {...this.props} />
                <Outlet />
            </React.Fragment>
        )
    }
}

const Shop = props => {
    const navigate = useNavigate();
    const location = useLocation();
    const extra = { navigate, location }
    return <ShopRender {...props} {...extra} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Shop);

const doc = {
    title: "Main Shop page",
    description: [
        ["Shop page has all the items, categories, offers and latest items. Items on offer should be visible"],
        ["TODO:"],
        ["- Less is done at this point. Only major functionality is implemented. " +
            "However, having the basis of developing this page makes it easier " +
            "to improve both UI and other functionality."],
    ]
}