import React, { Component } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux'
import redux from '../../ReduxMapper';
import Utils from '../../utils/utils';
import Api, { APICall } from '../../Api';
import component from '../../Components';
import AsideCartInfo from './AsideCartInfo';
import ShipAdd from './ShipAdd';


class ShippingComponent extends Component {
   constructor(props) {
      super(props)
      this.state = {
         error: null,
         success: null,
         loading: false,
         loadme: false,
         userAddress: null,
         form_error: false,
         first_name: "", last_name: "", phone: "", address: "", state: ""
      }
   }

   /**
    * @summary Check if the order is empty. If empty redirect to shop > products
    * @static
    * @param {*} props
    * @param {*} state
    * @return {*} 
    * @memberof ShippingComponent
    */
   static getDerivedStateFromProps(props, state) {
      const isNull = props.cartProducts.cartItems === null
      if (!isNull) {
         if (Object.entries(props.cartProducts.cartItems).length === 0)
            props.navigate("/shop/products")
      }
      return null;
   }

   retriveAddress = () => {
      this.setState({ loading: true, error: null, success: null, userAddress: null })
      APICall(Api.retriveAddress(), success => {
         if (success.data.address) {
            this.setState({
               loading: false, error: null, success: null, userAddress: success.data.address
            })
            return
         }
         this.setState({ loading: false, error: null, success: success.data.details, userAddress: success.data.address })
      }, error => {
         if (error.data)
            this.setState({ error: error.data.details, loading: false });
         if (error.error)
            this.setState({ loading: false, error: error.error })
      }, "retrieving your address")

   }

   /**
    * @summarry Retrieve Shipping Address if any and setstate
    * @memberof ShippingComponent
    */
   componentDidMount() {
      this.retriveAddress();
   }

   defaultAPICall = (obj, useCurrent = false, event = null) => {
      this.setState({ loading: true })
      return APICall(Api.createAddress(obj), success => {
         if (this.state.form_error) {
            this.setState({ form_error: false })
            for (const element of event.target.elements)
               try {
                  element.classList.remove("did-error-input")
                  element.nextSibling.classList.remove("did-error-input")
                  element.nextSibling.innerHTML = element.getAttribute("data-name")
               } catch (error) { }
         }
         if (success.data.redirect) {
            this.props.localCartUpdated(success.data.order);
            if (useCurrent)
               setTimeout(() => {
                  this.props.navigate("/cart/order-review", { replace: true })
               }, 1000);
         } else {
            this.retriveAddress();
            this.setState({ loading: false, error: null, success: success.data.details })
         }


      }, error => {
         this.setState({ loading: false, form_error: true })
         if (typeof error.data.details === "object") {
            const field = error.data.details;
            if (event) {
               for (const element of event.target.elements) {
                  for (const error of Object.keys(field)) {
                     if (error === element.name) {
                        element.classList.add("did-error-input")
                        element.nextSibling.classList.add("did-error-input")
                        element.nextSibling.innerHTML = field[error][0]
                     }
                  }
               }
            }
            return
         }
         this.setState({ loading: false })
      }, "creating shipping address")
   }

   useCurrent = id => {
      let obj = {
         "useCurrent": true,
         'id': id
      }
      return this.defaultAPICall(obj, true)
   }

   handleBillingForm = event => {
      event.preventDefault()
      let country = "KE"
      let obj = {};
      const { first_name, last_name, phone, address, state } = this.state
      obj = { first_name, last_name, phone, address, state, country }
      return this.defaultAPICall(obj, false, event)
   }

   render() {
      const { authentication: { profile }, cartProducts: { cartItems } } = this.props;
      const is_cart = cartItems !== null && Object.entries(cartItems).length !== 0;
      const { error, success, loading, loadme, userAddress, first_name, last_name, phone, address, state } = this.state;
      return (
         <React.Fragment>
            <component.BreadCrumb
               title={"Billing address"}
               current={"Your address"}
               links={[
                  { name: 'Home', to: '/' },
                  { name: 'Shop', to: '/shop/products/' },
                  { name: 'Cart', to: '/cart/cart-view/' }
               ]}
            />

            <div className="ShippingAddress">
               <div className='container my-2'>
                  <div className="my-2">
                     {error && <div className="alert alert-danger">{error}</div>}
                     {success && <div className="alert alert-success ">{success}</div>}
                     {loading &&
                        <div className="d-flex flex-column align-items-center py-2">
                           <div className="spinner-border text-success" />
                        </div>}
                  </div>

                  <div className="row">
                     <div className="col-lg-12">
                        <div id="user__info" className="d-sm-flex justify-content-between align-items-center bg-light rounded-3 my-4">

                           <div className="p-4">
                              <div className="d-flex align-items-center">
                                 <div className="img-thumbnail rounded-circle position-relative flex-shrink-0">
                                    {profile &&
                                       <>
                                          {profile.avatar && <img src={profile.avatar} alt="Profile" className="rounded-circle" height="90" width="90" />}
                                       </>

                                    }

                                 </div>
                                 {profile &&
                                    <div className="ps-3 d-flex flex-column">
                                       {profile.account.first_name &&
                                          <h3 className="fs-base text-capitalize mb-0">
                                             {`${profile.account.first_name} ${profile.account.last_name}`}
                                          </h3>
                                       }
                                       {!profile.account.first_name && <span className="badge bg-warning text-dark align-text-bottom" style={{ cursor: 'pointer' }}>Profile update!</span>}
                                       <span className="text-accent fs-7">{profile.account.email}</span>
                                    </div>
                                 }
                              </div>

                           </div>


                        </div>
                     </div>
                  </div>

                  <div className="row">
                     <div className="col-md-12 col-lg-8">


                        <div className="accordion" id="accordionPanelsStayOpenExample">

                           {userAddress && userAddress.length > 0 &&
                              <div className="accordion-item mb-3">
                                 <h2 className="accordion-header" >
                                    <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#previous_address">
                                       Previous Addresses
                                    </button>
                                 </h2>
                                 <div id="previous_address" className="accordion-collapse collapse">
                                    {userAddress.map((addr, i) => {
                                       return (
                                          <React.Fragment key={i}>
                                             <ShipAdd {...addr} useCurrentFunc={this.useCurrent} />
                                          </React.Fragment>
                                       )
                                    })}
                                 </div>
                              </div>
                           }
                           <div className="accordion-item">
                              <h2 className="accordion-header" >
                                 <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#address_form">
                                    Create a new address
                                 </button>
                              </h2>
                              <div id="address_form" className="accordion-collapse collapse">
                                 <div className="accordion-body">

                                    {!loadme &&
                                       <form className='billingForm' onSubmit={event => this.handleBillingForm(event)}>
                                          <div className={``}>

                                             <div className="row g-3">
                                                <div className="col-sm-6">
                                                   <div className="did-floating-label-content">
                                                      <input className=" did-floating-input" type="text" name='first_name' data-name="First name" placeholder="" value={first_name} onChange={e =>
                                                         this.setState({ first_name: e.target.value })} required />
                                                      <label className="did-floating-label">First name</label>
                                                   </div>
                                                </div>
                                                <div className="col-sm-6">
                                                   <div className="did-floating-label-content">
                                                      <input className=" did-floating-input" type="text" name='last_name' data-name="Second name" placeholder="" value={last_name} onChange={e =>
                                                         this.setState({ last_name: e.target.value })} required />
                                                      <label className="did-floating-label">Second name</label>
                                                   </div>
                                                </div>
                                                <div className="col-sm-6">
                                                   <div className="did-floating-label-content">
                                                      <input className="did-floating-input" type="text" name='phone' data-name="Phone number (+123 ... )" placeholder="" value={phone} onChange={e =>
                                                         this.setState({ phone: e.target.value })} required />
                                                      <label className="did-floating-label">Phone number (+123 ... )</label>
                                                   </div>
                                                </div>
                                                <div className="col-sm-6">
                                                   <div className="did-floating-label-content">
                                                      <input className=" did-floating-input" type="text" name='address' data-name="Address /Town" placeholder="" value={address} onChange={e =>
                                                         this.setState({ address: e.target.value })} required />
                                                      <label className="did-floating-label">Address /Town</label>
                                                   </div>
                                                </div>
                                                <div className="col-sm-12">
                                                   <div className="did-floating-label-content">
                                                      <input className=" did-floating-input" type="text" name='state' data-name="ZIP code /County" placeholder="" value={state} onChange={e =>
                                                         this.setState({ state: e.target.value })} required />
                                                      <label className="did-floating-label">ZIP code /County</label>
                                                   </div>
                                                </div>
                                             </div>

                                             <div className="d-flex pt-4">
                                                <div className="w-50 pe-3">
                                                   <Link className="btn btn-secondary d-block w-100" to="/cart/cart-view">
                                                      <i className="bi bi-arrow-left mt-sm-0 me-1" />
                                                      <span className="d-none d-sm-inline">Back to Cart</span>
                                                      <span className="d-inline d-sm-none">Back</span>
                                                   </Link>
                                                </div>
                                                {/* to="/shop/shipping-method" */}
                                                <div className="w-50 ps-2">
                                                   <button type='submit' disabled={loading} className="btn btn-success w-100 d-flex justify-content-center">
                                                      <i className="bi bi-envelope-plus mt-sm-0 me-2 fs-6" />
                                                      <span className="d-none d-sm-inline">Add shipping address</span>
                                                      <span className="d-inline d-sm-none">Add</span>
                                                      {/* {loading && <div id="preloader" />} */}
                                                   </button>
                                                </div>
                                             </div>
                                          </div>
                                       </form>
                                    }
                                 </div>
                              </div>
                           </div>
                        </div>
                     </div>

                     {is_cart && <AsideCartInfo {...cartItems} />}
                  </div>

               </div>
            </div>
         </React.Fragment >
      )
   }
}

const ShippingAddress = (props) => {
   const navigate = useNavigate()
   const location = useLocation()
   const properties = Utils.objectUpdate(props, {
      navigate, location
   })

   return <ShippingComponent {...properties} />
}



export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(ShippingAddress);
