import React from 'react'
import { connect } from 'react-redux'

export const ShipAdd = (props) => {
    const { first_name, last_name, address, phone, state, id } = props;
    return (
        <div className="col-lg-12 mb-3">
            <div className="card p-1 border-primary">
                <div className="row">
                    <div className="col-lg-3 col-md-6 d-flex align-items-center my-lg-0 my-md-3 my-2">
                        <p className='fs-7'><strong>Name: </strong> <br />  {first_name} {last_name}</p>
                    </div>
                    <div className="col-lg-3 col-md-6 d-flex  flex-column my-lg-0 my-md-3 my-2">
                        <p className='fs-7 mb-1'><strong>Address: </strong> <br />{address}</p>
                        <p className='fs-7 mb-1'><strong>State: </strong> <br />{state}</p>
                    </div>
                    <div className="col-lg-3 col-md-6 d-flex align-items-center my-lg-0 my-md-3 my-2">
                        <p className='fs-7'><strong>Phone: </strong> <br />{phone}</p>
                    </div>
                    <div className="col-lg-3 col-md-6 d-flex align-items-center my-lg-0 my-md-3 my-2" onClick={e => props.useCurrentFunc(id)}>
                        <button className="btn btn-warning text-dark">Select this address</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({})

const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(ShipAdd)