import React, { Component } from 'react'
import component from "../Components";
import redux from '../ReduxMapper'
import { connect } from 'react-redux';
import DevInfo from '../DevInfo';


class Home extends Component {

  render() {
    return (
      <React.Fragment>
        <DevInfo {...doc} />
        <component.Nav1 {...this.props} />
        <component.Hero />
        <component.HomeProducts {...this.props} />
        <component.Footer1 />
      </React.Fragment>
    )
  }
}

export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Home);


