import React, { Component } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux'
import redux from '../../ReduxMapper';
import Utils from '../../utils/utils';
import component from '../../Components';
import Api, { APICall } from '../../Api';



class CompleteOrderComponent extends Component {
    state = {
        loading: false,
        success: null,
        error: null,
        data: null
    }
    retrieveCompletedOrder = () => {
        this.setState({ loading: true });
        APICall(Api.completedOrder(), success => {
            this.setState({ loading: false, data: success.data });
        }, error => {
        }, "retrieving completed order")
    }
    componentDidMount() {
        this.retrieveCompletedOrder()
    }
    render() {
        const { loading, data } = this.state;
        return (
            <React.Fragment>

                <component.BreadCrumb
                    title={"Thank you for your order!"}
                    current={"Order completed"}
                    links={[
                        { name: 'Home', to: '/' },
                        { name: 'Shop', to: '/shop/products/' },
                        { name: 'View profile', to: '/profile/dashboard/' },
                    ]}
                />

                <div className="container mb-sm-4">
                    <div className="pt-1">
                        <div className="card mt-sm-3 border-0">
                            <div className="card-body text-center ">
                                {loading && <div className="spinner-border text-info" />}
                                {!loading && data && data.length > 0 &&
                                    <React.Fragment>
                                        <p className="fs-6 mb-2">Your order has been placed and will be processed as soon as possible.</p>
                                        <p className="fs-6 mb-2">Make sure you make note of your order number, which is <span className="fw-bold">{data[0].ref_code}</span> </p>
                                        <p><span className='text-muted'>View your</span> profile for more details about your order.</p>
                                        <p className="fs-6">You will be receiving an email shortly with confirmation of your order.</p>
                                        <Link className="btn btn-sm btn-secondary mt-3 me-3" to="/shop/products">Go back shopping</Link>
                                        <Link className="btn btn-sm btn-primary mt-3" to="/profile/dashboard">
                                            <i className="bi bi-geo-alt-fill" />
                                            Track order
                                        </Link>
                                    </React.Fragment>}
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

const CompleteOrder = (props) => {
    const navigate = useNavigate()
    const location = useLocation()
    const properties = Utils.objectUpdate(props, {
        navigate, location
    })

    return <CompleteOrderComponent {...properties} />
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(CompleteOrder);
