import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class Footer1 extends Component {
    render() {
        return (
            <footer id="footer" className="footer">

                <div className="footer-content">
                    <div className="container">
                        <div className="row gy-4">
                            <div className="col-lg-5 col-md-12 footer-info">
                                <a href="/" className="logo d-flex align-items-center">
                                    <span>Innovest Shop<span className="text-primary fs-1">.</span> </span>
                                </a>
                                <p>Take a risk and keep testing, because what works today won't work tomorrow, but what worked yesterday may work again.</p>
                                <div className="social-links d-flex  mt-3">
                                    <a href="#" className="twitter"><i className="bi bi-twitter"></i></a>
                                    <a href="#" className="facebook"><i className="bi bi-facebook"></i></a>
                                    <a href="#" className="instagram"><i className="bi bi-instagram"></i></a>
                                    <a href="#" className="linkedin"><i className="bi bi-linkedin"></i></a>
                                </div>
                            </div>

                            <div className="col-lg-2 col-6 footer-links">
                                <h4>Useful Links</h4>
                                <ul>
                                    <li><i className="bi bi-dash"></i> <a href="/">Home</a></li>
                                    <li><i className="bi bi-dash"></i> <Link to="/legal/faqs">FAQs</Link></li>
                                    <li><i className="bi bi-dash"></i> <Link to="/legal/terms">Terms of service</Link></li>
                                    <li><i className="bi bi-dash"></i> <Link to="/legal/privacy">Privacy policy</Link></li>
                                </ul>
                            </div>

                            <div className="col-lg-2 col-6 footer-links">

                            </div>

                            <div className="col-lg-3 col-md-12 footer-contact text-center text-md-start">
                                <h4>Contact Us</h4>
                                <p>
                                    Nakuru,<br />
                                    Kenya <br /><br />
                                    <strong>Phone:</strong> +254 7964 876 62<br />
                                    <strong>Email:</strong> pminnovest@gmail.com<br />
                                </p>

                            </div>

                        </div>
                    </div>
                </div>

                <div className="footer-legal">
                    <div className="container">
                        <div className="copyright">
                            &copy; Copyright <strong><span>Innovest Shop</span></strong>. All Rights Reserved
                        </div>
                        <div className="credits">
                            Designed by <a href="/">[Credits]</a>
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
}
