import { configureStore } from "@reduxjs/toolkit";
import authLoginReducer from "./auth/AuthLoginReducer";
import CartActionsReducer from "./cart/CartActionsReducer";
import HomeProductsReducer from "./home/HomeProductsReducer";
import profileReducer from "./profile/profileReducer";


export const store = configureStore({
    reducer: {
        authentication: authLoginReducer,
        homeProducts: HomeProductsReducer,
        cartActions: CartActionsReducer,
        profileActions: profileReducer,
    },
    devTools: false,
})
