import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Main from '../Main'
export default class Nav1 extends Component {
    constructor(props) {
        super(props)
        this.state = {
            sticked: false,
            render: false,
            cart_items: null
        }
    }
    static getDerivedStateFromProps(props, state) {
        let sticked = false;
        if (props.location) {
            const path = props.location.pathname.split('/');
            if (path.slice(1, 3).join('/') + '/' === 'shop/product/')
                return {
                    sticked: sticked,
                }
        }
        return null
    }
    componentDidMount() {
        Main.headerFixed();
        if (!this.state.sticked) {
        }
        let body = document.querySelector('.mobile-nav-active');
        const mobileNavShow = document.querySelector('.mobile-nav-show');
        const mobileNavHide = document.querySelector('.mobile-nav-hide');
        if (body) {
            body.classList.toggle("overflow-hidden")
            mobileNavShow.classList.toggle('d-none');
            mobileNavHide.classList.toggle('d-none');
        }
    }
    dropdownToggle = e => {
        e.preventDefault();
        if (document.querySelector('.mobile-nav-active')) {
            const dropdown = e.currentTarget;
            dropdown.classList.toggle('active');
            dropdown.nextElementSibling.classList.toggle('dropdown-active');
            let dropDownIndicator = dropdown.querySelector('.dropdown-indicator');
            dropDownIndicator.classList.toggle('bi-chevron-up');
            dropDownIndicator.classList.toggle('bi-chevron-down');
        }
    }

    mobileNavToogle = e => {
        e.preventDefault();
        const mobileNavShow = document.querySelector('.mobile-nav-show');
        const mobileNavHide = document.querySelector('.mobile-nav-hide');
        document.querySelector('body').classList.toggle('mobile-nav-active');
        mobileNavShow.classList.toggle('d-none');
        mobileNavHide.classList.toggle('d-none');
    }
    render() {
        const { authentication: { isAuthenticated, profile }, authLogout, cartProducts: { cartItems } } = this.props;
        return (

            <React.Fragment>
                <section id="topbar" className="d-flex align-items-center">
                    <div className="container d-flex justify-content-center justify-content-md-between">
                        <div className="contact-info d-flex align-items-center">
                            <i className="bi bi-house-fill fs-5 text-primary"></i>
                            <a href={`${localStorage.getItem('host')}`}>pminnovest.com</a>
                        </div>
                        <div className="social-links d-none d-md-inline-block">
                            <Link className="text-warning" to="/developer"><i className="bi bi-gear fs-6 me-1"></i> Developer</Link>
                        </div>
                    </div>
                </section>

                <header id="header" className={`header d-flex align-items-center fixed-top `} >
                    <div className="container-fluid container-xl d-flex align-items-center justify-content-between">

                        <Link to="/" className="logo d-flex align-items-center">
                            {/* Uncomment the line below if you also wish to use an image logo */}
                            {/* <img src="assets/img/logo.png" alt="">  */}
                            <h1 className="d-flex align-items-center">Innovest Shop</h1>
                        </Link>

                        <i className="mobile-nav-toggle mobile-nav-show bi bi-list" onClick={e => this.mobileNavToogle(e)} />
                        <i className="mobile-nav-toggle mobile-nav-hide d-none bi bi-x" onClick={e => this.mobileNavToogle(e)} />

                        <nav id="navbar" className="navbar">
                            <ul>
                                <li><Link to="/" className="active">Home</Link></li>
                                <li><Link to="/legal/contact/" className="active">Contact us</Link></li>
                                {isAuthenticated &&
                                    <li className="dropdown">
                                        <a href="/" onClick={e => this.dropdownToggle(e)}>
                                            {profile &&
                                                <span className='fw-bold' >
                                                    {profile.account.first_name && <>{profile.account.first_name} {profile.account.last_name}</>}
                                                    {!profile.account.first_name && <>{profile.account.email}</>}
                                                </span>}
                                            {!profile && <span className='fw-bold' >Profile</span>}
                                            <i className="bi bi-chevron-down dropdown-indicator"></i>
                                        </a>
                                        <ul>
                                            <li><Link to="/profile/dashboard">Your account</Link></li>
                                            <li><Link to="/profile/dashboard">Your orders</Link></li>
                                            <li><Link to="/profile/settings">Settings</Link></li>
                                            <li className='d-flex justify-content-center border-top pt-3 mt-3'>
                                                <button type="button" className="btn btn-sm btn-warning px-lg-3 w-50"
                                                    onClick={e => authLogout()}
                                                >Sign out</button>
                                            </li>
                                        </ul>
                                    </li>
                                }

                                {!isAuthenticated && <li><Link to="/auth/login">Sign in</Link></li>}
                                {!isAuthenticated && <li>
                                    <Link to="/auth/signup">
                                        <button type="button" className="btn btn-sm btn-outline-light">Sign up</button>
                                    </Link>
                                </li>}
                                {isAuthenticated && <li>
                                    <Link to="/auth/signup">
                                        <button type="button" className="btn btn-sm btn-outline-light">Add user</button>
                                    </Link>
                                </li>}


                            </ul>
                            {cartItems && isAuthenticated &&
                                <div className="navbar-tool dropdown ms-3">
                                    <Link className="navbar-tool-icon-box bg-secondary" to="/cart/cart-view/">
                                        <span className="navbar-tool-label bg-danger">
                                            {cartItems.items.length}
                                        </span>
                                        <i className="navbar-tool-icon bi bi-cart-fill"></i>
                                    </Link>
                                    <Link className="navbar-tool-text fw-bold" to="/cart/cart-view/">
                                        <small>My cart</small>${(cartItems.order_total).toFixed(2)}
                                    </Link>
                                    {cartItems &&
                                        <ul className='cart'>
                                            {
                                                cartItems.items.map(item => {
                                                    return (
                                                        <li key={item.id}>
                                                            <div className="d-flex align-items-center p-1" >
                                                                <Link className="flex-shrink-0" to={`/shop/product/${item.item.slug}/`}>
                                                                    <img src={`${localStorage.getItem("host")}${item.item.image}`} alt="Product" width="64" />
                                                                    <img src={`${item.item.image}`} alt="Product" width="64" />
                                                                </Link>
                                                                <div className="ps-2">
                                                                    <h6 className="item-title">
                                                                        <Link to={`/shop/product/${item.item.slug}/`}>{item.item.title}</Link>
                                                                    </h6>
                                                                    <div className="item-info">
                                                                        <span className="text-accent me-2">
                                                                            {item.item.discounted_price > 0 && <span className='me-2'> ${item.item.discounted_price}</span>}
                                                                            {item.item.discounted_price === 0 && <span>${item.item.price}</span>}
                                                                        </span>
                                                                        <span className="text-muted">x {item.quantity}</span>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </li>
                                                    )
                                                })
                                            }
                                            <div className="d-flex flex-wrap justify-content-between align-items-center py">
                                                <div className="fs-sm me-2 py-2 px-3">
                                                    <span className="text-muted">Subtotal:</span>
                                                    <span className="text-accent fs-base ms-1">
                                                        <small className='fw-bold'>${(cartItems.order_total).toFixed(2)}</small>
                                                    </span>
                                                </div>

                                            </div>
                                            <li className='p-3 d-flex justify-content-between'>
                                                <Link className="btn btn-warning btn-sm px-4 text-dark" to="/cart/shipping-address">
                                                    <i className="bi bi-credit-card-2-back-fill  me-2 fs-base align-middle" /> Checkout
                                                </Link>
                                                <Link className="btn btn-primary btn-sm text-light" to="/cart/cart-view/">Expand cart
                                                    <i className="bi bi-chevron-right ms-1 me-1 fs-6"></i>
                                                </Link>
                                            </li>
                                        </ul>

                                    }
                                </div>
                            }
                        </nav>
                    </div>
                </header>

            </React.Fragment >
        )
    }
}
