import React, { Component } from 'react'
import { connect } from 'react-redux'
import Api, { APICall } from '../../Api'
import redux from '../../ReduxMapper'

class Contact extends Component {
    state = {
        loading: false,
        error: null,
        success: null,
        data: null
    }
    handleSendMessage = e => {
        e.preventDefault();
        this.setState({ loading: true });
        const formData = new FormData(e.target);
        APICall(Api.sendMessage(formData), success => {
            const data = success.data;
            if (data.success)
                this.setState({ loading: false, success: data.success, error: null })
            if (data.error)
                this.setState({ loading: false, error: data.success, success: null })
        }, error => {
            console.log(error);
            this.setState({ loading: false, error: JSON.stringify(error) })
        }, 'sending contact message');
    }
    render() {
        const { loading, error, success } = this.state;
        return (
            <section className="section contact_page">

                <div className="container">
                    <div className="row gy-4">

                        <div className="col-xl-5">

                            <div className="row">
                                <div className="col-lg-6">
                                    <div className="info-box card">
                                        <i className="bi bi-geo-alt"></i>
                                        <h3>Address</h3>
                                        <p>Nakuru,<br />Kenya</p>
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <div className="info-box card">
                                        <i className="bi bi-telephone"></i>
                                        <h3>Call Us</h3>
                                        <p>+254 7247 734 26<br />+254 7964 876 62</p>
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <div className="info-box card">
                                        <i className="bi bi-envelope"></i>
                                        <h3>Email Us</h3>
                                        <p>pminnovest@gmail.com</p>
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <div className="info-box card">
                                        <i className="bi bi-clock"></i>
                                        <h3>Open Hours</h3>
                                        <p>Monday - Friday<br />9:00AM - 05:00PM</p>
                                    </div>
                                </div>
                            </div>

                        </div>

                        <div className="col-xl-7">
                            <div className="card p-4">
                                <form className="contact_form" onSubmit={e => this.handleSendMessage(e)}>
                                    <div className="row gy-4">

                                        <div className="col-md-6">
                                            <input type="text" name="names" className="form-control" placeholder="Your Name" required />
                                        </div>

                                        <div className="col-md-6 ">
                                            <input type="email" className="form-control" name="email" placeholder="Your Email" required />
                                        </div>

                                        <div className="col-md-12">
                                            <input type="text" className="form-control" name="subject" placeholder="Subject" required />
                                        </div>

                                        <div className="col-md-12">
                                            <textarea className="form-control" name="message" rows="6" placeholder="Message" required></textarea>
                                        </div>

                                        <div className="col-md-12 text-center">
                                            {loading && <div className="loading">Loading</div>}
                                            {error &&
                                                <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                                    <i className="bi bi-exclamation-diamond-fill fs-3 me-3"></i> <strong>Error: </strong> {error}
                                                    <button type="button" className="btn-close" onClick={e => this.setState({ error: null })}></button>
                                                </div>
                                            }
                                            {success &&
                                                <div className="alert alert-success alert-dismissible fade show" role="alert">
                                                    <i className="bi bi-check-circle-fill fs-3 me-3"></i> <strong>Success: </strong> {success}
                                                    <button type="button" className="btn-close" onClick={e => this.setState({ success: null })}></button>
                                                </div>
                                            }
                                            {!loading && <button type="submit">Send Message</button>}
                                        </div>

                                    </div>
                                </form>
                            </div>

                        </div>

                    </div>
                </div>

            </section>

        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Contact)