import React, { Component } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux'
import ReduxMapper from '../../ReduxMapper';
import Utils from '../../utils/utils';
import AsideCartInfo from './AsideCartInfo'
import component from '../../Components';
import Api, { APICall } from '../../Api';

class PaymentMethodComponent extends Component {
    state = {
        loading: false,
        error: null,
        success: null,
        payment: null
    }
    uuidv4 = () => {
        return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
            (c ^ (crypto.getRandomValues(new Uint8Array(1))[0] & 15) >> c / 4).toString(16)
        )
    }

    handleMpesaPay = e => {
        e.preventDefault()
        this.setState({ loading: true })
        let pay_id = this.uuidv4(), amount = 0
        if (this.props.cartProducts)
            amount = this.props.cartProducts.cartItems.order_total;
        APICall(Api.mpesaPay({ pay_id, amount }), success => {
            this.props.getCart()
            this.setState({ loading: false, success: success.data.details })
        }, error => {
            this.setState({ loading: false, error: error.data.details })
        }, "making order payment")
    }
    static getDerivedStateFromProps(props, state) {
        if (props.cartProducts.cartItems) {
            if (props.cartProducts.cartItems.payment)
                if (props.cartProducts.cartItems.payment.amount !== null)
                    if (props.cartProducts.cartItems.payment !== state.payment) {
                        return {
                            payment: props.cartProducts.cartItems.payment,
                            loading: false,
                            success: "Successfully transacted through Mpesa"
                        }
                    }
        }
        return null;
    }

    render() {
        const { cartProducts: { cartItems }, navigate } = this.props;
        const is_cart = cartItems !== null && Object.entries(cartItems).length !== 0;
        if (this.state.success) {
            setTimeout(() => {
                navigate("/complete-order/", { replace: true })
            }, 1000);
        }
        return (
            <React.Fragment>
                <component.BreadCrumb
                    title={"Payment"}
                    current={"Payment"}
                    links={[
                        { name: 'Home', to: '/' },
                        { name: 'Shop', to: '/shop/products/' },
                        { name: 'Cart', to: '/cart/cart-view/' },
                        { name: 'Order review', to: '/cart/order-review' }
                    ]}
                />
                <section className="paymentMethod">
                    <div className='container'>
                        <div className="row mt-lg-5">

                            <div className="col-lg-8 col-md-12 mb-5">
                                <h4 className="mb-3 d-none d-lg-block">Choose payment method</h4>
                                {this.state.success &&
                                    <div className="alert alert-success">
                                        {this.state.success}
                                    </div>
                                }
                                {this.state.error &&
                                    <div className="alert alert-danger">
                                        {this.state.error}
                                    </div>
                                }
                                {!this.state.success && <form className="form-floating">
                                    <input type="email" className="form-control" placeholder="name@example.com" />
                                    <label>Enter Mpesa number</label>
                                </form>}


                                <div className="d-flex pt-4">
                                    <div className="w-50 pe-3">
                                        <Link className="btn btn-secondary d-block w-100" to="/cart/order-review" disabled={true}>

                                            <i className="bi bi-arrow-left mt-sm-0 me-1" />
                                            <span className="d-none d-sm-inline">Back to reviewing </span>
                                            <span className="d-inline d-sm-none">Back</span>
                                        </Link>
                                    </div>

                                    <div className="w-50 ps-2">
                                        {!this.state.loading &&
                                            <Link className="btn btn-warning d-block w-100" to="/complete-order" onClick={e => this.handleMpesaPay(e)}>
                                                <React.Fragment>
                                                    <span className="d-none d-sm-inline">Complete order</span>
                                                    <span className="d-inline d-sm-none">Complete</span>
                                                    <i className="bi bi-arrow-right mt-sm-0 ms-1" />
                                                </React.Fragment>
                                            </Link>}
                                        {this.state.loading &&
                                            <div className="text-center">
                                                <div className='spinner-border text-warning' />
                                            </div>
                                        }
                                    </div>
                                </div>
                            </div>
                            {is_cart &&
                                <AsideCartInfo {...cartItems} />
                            }
                        </div>
                    </div>
                </section>

            </React.Fragment>

        )
    }
}

const Payment = (props) => {
    const navigate = useNavigate()
    const location = useLocation()
    const properties = Utils.objectUpdate(props, {
        navigate, location
    })

    return <PaymentMethodComponent {...properties} />
}



export default connect(ReduxMapper.mapStateToProps, ReduxMapper.mapDispatchToProps)(Payment);
