import React, { Component } from 'react'
import { connect } from 'react-redux'
import redux from './ReduxMapper'

class DevInfo extends Component {

    render() {
        const { title, description } = this.props;
        return (
            <div className='dev__info'>
                <div className="position-relative" id="container__info" >
                    <div className="wrapper">
                        <span className="figcaption-icon js-expand" onClick={e => {
                            e.preventDefault();
                            e.currentTarget.firstElementChild.classList.toggle("fs-4")
                            document.querySelector(".js-expand").classList.toggle("is-expanded");
                            document.querySelector(".figcaption").classList.toggle("is-expanded");
                            document.querySelector(".wrapper").classList.toggle("is-expanded");
                            document.querySelector("#container__info").classList.toggle("is-expanded");
                        }} >
                            <i className="bi bi-gear" />
                        </span>

                        <figcaption className="figcaption" >
                            {title && <div className="caption-title" dangerouslySetInnerHTML={{ __html: title }} />}
                            <div className="caption-copy-wrap">
                                {description && description.map((p, i) => <p className="caption-copy" key={i}>{p}</p>)}
                            </div>
                        </figcaption>
                    </div>
                </div >
            </div >
        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(DevInfo)