import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Outlet } from 'react-router-dom'
import redux from '../ReduxMapper'
import DevInfo from "../DevInfo";
import Nav1 from '../container/Nav1';
import BreadCrumb from '../container/BreadCrumb';
import Footer1 from '../container/Footer1';

class Legal extends Component {
  render() {
    return (
      <React.Fragment>
        <Nav1 {...this.props} />
        <BreadCrumb />

        <DevInfo {...doc} />
        <Outlet />
        <Footer1 {...this.props} />
      </React.Fragment>

    )
  }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Legal)
const doc = {
  title: "Legal page",
  description: [
    ["All legal documentation including licensing will be viewed"],
    ["  TODO:"],
    ["          - Flag user who have complied with relevant terms"],
    ["--------------------------------------------------------------------------------------"],
  ]
}