import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import Api from "../../Api";


const initialState = {
    cartLoader: false,
    cartItems: null,
    cartData: null,
    cartInfo: null,
    cartError: null
}

export const orderSummary = createAsyncThunk("cart/cartitems", async () => {
    const response = await Api.orderSummary();
    return response.data;
})



export const cartActionsSlice = createSlice({
    name: "cart",
    initialState,
    reducers: {
        cartUpdated: (state, action) => {
            if (!state.homeProductsLoader) {
                state.cartLoader = false
                state.cartError = null
                state.cartInfo = null
                state.cartItems = action.payload
                state.cartData = action
            }
        }
    },
    extraReducers(builder) {
        builder
            .addCase(orderSummary.pending, (state, action) => {
                state.cartLoader = true
                state.cartError = null
                state.cartItems = null
                state.cartData = null
                state.cartInfo = null
            })
            .addCase(orderSummary.fulfilled, (state, action) => {
                state.cartLoader = false
                state.cartError = null
                state.cartInfo = null
                state.cartItems = action.payload
                state.cartData = action
            })
            .addCase(orderSummary.rejected, (state, action) => {
                state.cartLoader = false
                state.cartError = action.error.message
                state.cartInfo = null
                state.cartItems = null
                state.cartData = action
            })
    }
})
export const { cartUpdated } = cartActionsSlice.actions;
export default cartActionsSlice.reducer;