import React from 'react'
import { Link } from 'react-router-dom'
import Api, { APICall } from '../../Api'


const CartItemRow = ({ item: { title, discounted_price, price, image, slug, stock }, localCartUpdated, quantity, total, review }) => {
    const increaseQuantity = e => APICall(Api.addItemsToCart(slug), response => {
        if (response.data.item) {
            localCartUpdated(response.data.order)
        }
    });

    const decreaseQuantity = e => APICall(Api.reduceFromCart(slug), response => {
        if (response.data.item) {
            localCartUpdated(response.data.order)
        }
    });
    // const host = localStorage.getItem("host")
    const deleteFromCart = () => APICall(Api.deleteFromCart(slug), response => {
        localCartUpdated(response.data.order)
    });
    return (
        <div className="d-sm-flex justify-content-between align-items-center my-2 pb-3 border-bottom">
            <div className="d-block d-sm-flex align-items-center text-center text-sm-start">
                <Link className="d-inline-block flex-shrink-0 mx-auto me-sm-4" to={`/shop/product/${slug}/`}>
                    {/* <img src={`${host}${image}`} alt="Product" width="160" /> */}
                    <img src={`${image}`} alt="Product" width="160" />
                </Link>
                <div className="pt-2">
                    <h3 className="product-title fs-6 mb-2">
                        <Link to={`/shop/product/${slug}/`}>{title}</Link>
                    </h3>
                    {/* <div className="fs-sm"><span className="text-muted me-2">Size:</span>8.5</div>*/}
                    <div className="fs-sm"><span className="text-muted me-2">Price:</span>
                        {discounted_price > 0 && <span className='me-2'>@ ${discounted_price}</span>}
                        {discounted_price > 0 && <em className="text-decoration-line-through text-muted ">${price}</em>}
                        {discounted_price === 0 && <span>@ ${price}</span>}
                    </div>
                    <div className="text-accent pt-2">
                        <span className="text-muted">Total:</span> ${(total).toFixed(2)}
                    </div>
                </div>
            </div>
            <div className="pt-2 pt-sm-0 ps-sm-3 mx-auto mx-sm-0 text-center text-sm-start" style={{ width: "10rem" }}>
                <button className="btn btn-outline-light text-dark w-100" disabled>Quantity {review && quantity}</button>
                {!review && <div className="btn-group my-2 w-100" >
                    <button className="btn btn-outline-light text-dark" onClick={e => decreaseQuantity(e)}>
                        <i className="bi bi-dash-circle" />
                    </button>
                    <button className="btn btn-outline-light text-dark" disabled>{quantity}</button>
                    <button className="btn btn-outline-light text-dark" onClick={e => {
                        if (stock > 0) {
                            increaseQuantity(e)
                        }
                    }} disabled={stock < 1}>
                        {stock > 0 ? <i className="bi bi-plus-circle" /> : <span className='fs-8 text-decoration-line-through'>Stock</span>}
                    </button>
                </div>}
                {!review && <span className='text-danger btn btn-outline-light w-100' onClick={() => deleteFromCart(slug)}>
                    <i className="bi bi-x-circle" /> Remove
                </span>}
                {review && <Link to="/cart/cart-view" className='text-danger btn btn-outline-light w-100'>
                    <i className="bi bi-pencil-square" /> Edit
                </Link>}
            </div>
        </div>
    )
}



export default CartItemRow