import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import redux from '../../ReduxMapper'
import { connect } from 'react-redux';
import Api, { APICall } from '../../Api';

class Hero extends Component {

    render() {
        // localHomeProductsUpdate
        const { getHomeProducts, products: { homeProducts } } = this.props;
        return (
            <section id="hero" className="hero d-flex align-items-center">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-4">
                            <h2 data-aos="fade-up">Focus On What Matters</h2>
                            <blockquote data-aos="fade-up" data-aos-delay="100">
                                <p>Take a risk and keep testing, because what works today won't work tomorrow, but what worked yesterday may work again.</p>
                            </blockquote>
                            <div className="d-flex">
                                <Link to="/shop/products/" className="btn-get-started">Shop now</Link>
                            </div>
                            {homeProducts && homeProducts.length < 19 &&
                                <div className="d-flex py-1"
                                    onClick={e => {
                                        APICall(Api.autoCreateItems(), success => {
                                            // localHomeProductsUpdate(success.data.items)
                                            if (success.data.items)
                                                getHomeProducts()
                                        }, error => {
                                            console.log(error)
                                        }, "auto creating items: Developer")
                                    }}>
                                    <button className="btn btn-danger btn-sm">Get products: Developer</button>
                                </div>}
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
export default connect(redux.mapStateToProps, redux.mapDispatchToProps)(Hero);