import React from 'react'

const ToTop = () => {
  return (
    <span onClick={e => window.scrollTo({
      top: 0,
      behavior: 'smooth'
    })} className="scroll-top d-flex align-items-center justify-content-center">
      <i className="bi bi-arrow-up-short" />
    </span>
  )
}
export default ToTop;